import { Component } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { LANGUAGES } from 'app/modules/translate';
import { AuthService } from 'app/modules/authentication';
import { BehaviorSubject } from 'rxjs';
import { AlertService } from 'app/modules/core/services/alert.service';

@Component({
    selector: 'app-alert',
    templateUrl: './alert.component.html',
    styleUrls: ['./alert.component.css']
})
export class AlertComponent {

    public error: string;

    constructor(private alertService: AlertService) {
        // console.log('init alert composant');
        this.alertService.alertEmitterSubscriber().subscribe((message) => {
            // console.log('error:', message);
            this.displayAlert(message)
        });
    }

    private displayAlert(message: string): void {
        // console.log('display alert');
        this.error = message;
        setTimeout(() => {
            this.error = null;
        }, 5000);
    }

}



