import { NgModule } from '@angular/core';
import { HttpModule} from '@angular/http';
import { BrowserModule } from '@angular/platform-browser';
import { AuthGuard } from 'app/modules/authentication/services/auth.guard';
import { AuthService } from 'app/modules/authentication/services/auth.service';
import { LoginComponent } from 'app/modules/authentication/views/login.component';
import { AuthenticationRoutingModule } from 'app/modules/authentication/authentication-routing.modules';
import { HttpService } from 'app/modules/core/services/http.service';
import { LocalStorageModule, InfoLocalStorageService } from 'app/modules/localStorage';
import { XHRBackend, RequestOptions, Http } from '@angular/http';
import { Router } from '@angular/router';
import { AlertComponent } from 'app/modules/core/views/alert.component';
import { AlertService } from 'app/modules/core/services/alert.service';
import { TranslateModule } from 'app/modules/translate/translate.module';
import { CommonModule } from '@angular/common';
import { PathService } from './services/path.service';
import { AppConfigurationService } from './services/app-configuration.service';
import { TenantConfigurationService } from './services/tenant-configuration.service';
import { Office365LocalStorageService } from "app/modules/localStorage/services/office365LocalStorage.service";

export function httpFactory(backend: XHRBackend, options: RequestOptions, router: Router, infoLStorageService: InfoLocalStorageService,
    office365LocalStorageService: Office365LocalStorageService, alertService: AlertService, pathService: PathService) {
    return new HttpService(backend, options, router, infoLStorageService, office365LocalStorageService, alertService, pathService);
}

@NgModule({
    imports: [
        CommonModule,
        HttpModule,
        LocalStorageModule,
        TranslateModule,
    ],
    declarations: [
        AlertComponent,
    ],
    providers: [
        {
            provide: HttpService,
            useFactory: httpFactory,
            deps: [XHRBackend, RequestOptions, Router, InfoLocalStorageService, AlertService, PathService]
        },
        AlertService,
        PathService,
        AppConfigurationService,
        TenantConfigurationService,
    ],
    exports: [
        AlertComponent,
    ],
})
export class CoreModule { }



