import { Injectable } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ShowMapwizeComponent } from './show-mapwize.component';

@Injectable()
export class ShowMapwizeService {

  constructor(private modalService: NgbModal) { }

  public show(
    venue:any,
    resource:any): Promise<boolean> {
    const modalRef = this.modalService.open(ShowMapwizeComponent, { windowClass: 'modal-dialog-centered', size: 'lg' });
    modalRef.componentInstance.venue = venue;
    modalRef.componentInstance.resource = resource;

    return modalRef.result;
  }
}



