import { Component, OnInit, Input, Output, EventEmitter, ɵConsole } from '@angular/core';
import { PlaceWithChildren } from '../../../model/location/place-with-children';
import { Observable } from '../../../../../node_modules/rxjs';
import { TreeService } from '../../../services/tree/tree.service';
import { ResourceType } from 'app/model/location/sub/resource-type';
import { Place } from 'app/model/location/place';
import { LocationType } from 'app/model/location/location-type';

@Component({
    selector: 'app-place-selector',
    templateUrl: './place-selector.component.html',
    styleUrls: ['./place-selector.component.css']
})
export class PlaceSelectorComponent implements OnInit {

    primaryList: PlaceWithChildren[] = [];
    secondaryList: PlaceWithChildren[] = [];
    fullPrimaryList: PlaceWithChildren[] = [];
    fullSecondaryList: PlaceWithChildren[] = [];

    preferredPlaces: Place[] = [];
    placeTypesList: LocationType[] = [];

    @Input() selectedPrimary: PlaceWithChildren;
    @Input() selectedSecondary: PlaceWithChildren;
    @Input() selectedResourceType: ResourceType;
    @Output() onSelectPrimary = new EventEmitter();
    @Output() onSelectSecondary = new EventEmitter();
    selectedPrimaryLabel: string;
    selectedSecondaryLabel: string;
    secondaryTypelabel: string = "";

    constructor(private treeService: TreeService) { }

    ngOnInit() {
        Observable.forkJoin(
            this.treeService.getAllPlaces(),
            this.treeService.getFavouritePlaces(),
            this.treeService.getPlaceTypeList())
            .subscribe(data => {
                if (data) {
                    this.primaryList = data[0];
                    this.fullPrimaryList = data[0];

                    this.preferredPlaces = data[1];
                    this.placeTypesList = data[2];
                    if (this.preferredPlaces.length > 0) {
                        let primaryPlace: Place = this.preferredPlaces.find(p => p.id_place_parent === null);
                        //Fill primary
                        if(primaryPlace){
                            this.selectedPrimaryLabel = primaryPlace.label;
                            this.changePrimary(primaryPlace.label);
                            if (this.preferredPlaces.length > 1) {
                                let secondaryPlace: Place = this.preferredPlaces.find(p => p.id_place_parent === primaryPlace.id);
                                //Fill secondary
                                this.selectedSecondaryLabel = secondaryPlace.label;
                                this.changeSecondary(secondaryPlace.label);
                            }
                        }
                    }
                    else {
                        this.clearSelectedSecondary();
                        this.clearSelectedPrimary();
                    }
                }
            });

      
    }


    filterPlacesByResourceType(resourceType: ResourceType) {         
        
        this.filterPrimaryList(resourceType);
        this.filterSecondaryList(resourceType);

        let isPrimaryEmpty: boolean;
        let isSecondaryEmpty: boolean;

        if (this.selectedPrimary) {
            if (!this.primaryList.find(o => o.id == this.selectedPrimary.id)) {
                isPrimaryEmpty = true;
            }
        }

        if (this.selectedSecondary) {
            if (!this.secondaryList.find(o => o.id == this.selectedSecondary.id)) {
                isSecondaryEmpty = true;
            }
        }

        setTimeout(() => {
            if (isPrimaryEmpty || isSecondaryEmpty) {
                if (isPrimaryEmpty) {
                    this.onSelectPrimary.emit(null);
                }

                if (isSecondaryEmpty) {
                    this.onSelectSecondary.emit(null);
                }
            }
        }, 100);
    }

    filterPrimaryList(resourceType: ResourceType) {
        if (resourceType && this.fullPrimaryList) {           
            this.primaryList = this.fullPrimaryList.filter(o => resourceType.places.some(p => p.id == o.id));
        }
        else {
            this.primaryList = this.fullPrimaryList;
        }        
    }

    filterSecondaryList(resourceType: ResourceType) {
        if (resourceType && this.fullSecondaryList) {
            if (this.selectedPrimary) {
                this.secondaryList = this.fullSecondaryList.filter(o => resourceType.places.some(p => p.id == o.id));
            }
            else {
                this.secondaryList = [];
            }

        } else {
            this.secondaryList = this.fullSecondaryList;
        }
        if (this.secondaryList.length > 0) {
            let placeType: LocationType = this.placeTypesList.find(p => p.id === this.secondaryList[0].id_place_type);
            if (placeType) {
                this.secondaryTypelabel = placeType.label;
            }
            else {
                this.secondaryTypelabel = "";
            }
        }
    }

    changePrimary(prim) {
        
        if (this.primaryList.find(con => con.label == prim)) {
            this.selectedPrimary = this.primaryList.find(con => con.label == prim);
            this.fullSecondaryList = this.primaryList.find(con => con.label == prim).child_places;

            this.filterSecondaryList(this.selectedResourceType);

            if (this.selectedSecondary) {
                if (!this.secondaryList.some(o => o.id == this.selectedSecondary.id)) {
                    this.selectedSecondary = null;
                }
            }
        }
        else {
           
            this.selectedPrimary = null;
            this.selectedSecondary = null;
            this.selectedSecondaryLabel = null;
            this.secondaryList = null;
        }
        //It takes time for SearchPageRessources to be aware that selectedPrimary has changed
        setTimeout(() => {
            this.onSelectPrimary.emit(this.selectedPrimary);
            this.onSelectSecondary.emit(this.selectedSecondary);
        }, 100);

    }

    changeSecondary(prim) {
        if (this.secondaryList.find(con => con.label == prim)) {
            this.selectedSecondary = this.secondaryList.find(con => con.label == prim);
        }
        else {
            this.selectedSecondary = null;
        }
        //It takes time for SearchPageRessources to be aware that selectedSecondary has changed
        setTimeout(() => {
            this.onSelectSecondary.emit(this.selectedSecondary);
        }, 100);

    }

    clearSelectedPrimary() {
        this.selectedPrimary = null;
        this.selectedPrimaryLabel = null;
        this.changePrimary(null);
    }

    clearSelectedSecondary() {
        this.selectedSecondary = null;
        this.selectedSecondaryLabel = null;
        this.changeSecondary(null);
    }
}



