import { Component } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { LANGUAGES } from 'app/modules/translate';
import { AuthService } from 'app/modules/authentication';
import { BehaviorSubject } from 'rxjs';
import * as moment from 'moment';
import { Router, NavigationEnd } from '@angular/router';
import { AppConfigurationService } from 'app/modules/core';
import { Office365LocalStorageService } from './modules/localStorage/services/office365LocalStorage.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {

  public isNotOnLoginPage: boolean;
  public userDisplayName: string;

  constructor(
    private translate: TranslateService,
    private authService: AuthService,
    private office365LocalStorageService: Office365LocalStorageService,
    private appConfigurationService: AppConfigurationService,
    public router: Router) {

    translate.addLangs(LANGUAGES);
    translate.setDefaultLang(LANGUAGES[0]);
    translate.use(translate.getBrowserLang());

    this.userDisplayName = '';
    this.authService._isOnLoginPageAuthenticated$.subscribe((value) => {
      // SetTimeOut to avoid ExpressionChangedAfterItHasBeenCheckedError
      setTimeout(() => {
        this.isNotOnLoginPage = !value;
      }, 0);
    });

    if (this.appConfigurationService.env === "PROD") {
      this.router.events.subscribe(event => {
        if (event instanceof NavigationEnd) {
          (<any>window).ga('set', 'page', event.urlAfterRedirects);
          (<any>window).ga('send', 'pageview');
          if (this.office365LocalStorageService.userOffice365) {
            this.userDisplayName = this.office365LocalStorageService.userOffice365.givenName + ' ' + this.office365LocalStorageService.userOffice365.surname;
          }
        }
      })
    }
    else {
      this.router.events.subscribe(event => {
        if (event instanceof NavigationEnd) {
          if (this.office365LocalStorageService.userOffice365) {
            this.userDisplayName = this.office365LocalStorageService.userOffice365.givenName + ' ' + this.office365LocalStorageService.userOffice365.surname;
          }
        }
      })
    }
  }
}



