import { Component, ViewChild, ElementRef, Input, OnInit, SimpleChanges, OnChanges, OnDestroy } from '@angular/core';
import { UserLocal, AppConfigurationService } from 'app/modules/core';
import { AuthService } from 'app/modules/authentication';
import { InfoLocalStorageService } from 'app/modules/localStorage';
import { Globals } from 'app/globals';

@Component({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit, OnDestroy {

    // Not used during an Office365 authentication
    public user: UserLocal;
    public validationLoginState: boolean;
    public validationPasswordState: boolean;
    public checkboxRemember: { value: boolean };
    public env:string;
    public isWaitingForAuthToComplete: boolean;

    constructor(
        private authService: AuthService,
        private infoLocalStorageService: InfoLocalStorageService,
        private appConfigurationService: AppConfigurationService,
        private globals: Globals) { }

    public async ngOnInit(): Promise<void> {
        this.user = new UserLocal();
        this.validationLoginState = false;
        this.validationPasswordState = false;
        this.checkboxRemember = { value: false };
this.env = this.appConfigurationService.env;
        // hide navbar
        this.authService._isOnLoginPageAuthenticated$.next(true);
        this.authService.waitingForAuthenticationSubscriber().subscribe( (value) => this.validationLoginState = value);
        // Check where is the workflow of authentication.
        const isAuth = await this.authService.isAuthenticated();
        if (isAuth) {
            window.location.href = this.appConfigurationService.urlAccueil;
        }
    }

    public ngOnDestroy(): void {
        this.authService._isOnLoginPageAuthenticated$.next(false);
    }

    public async connect(): Promise<void> {
        await this.authService.login();
    }
}



