import { Pipe, PipeTransform } from '@angular/core';

import { Diacritics } from 'app/model/utils'

@Pipe({
  name: 'reportLocationSearch'
})
export class ReportLocationSearchPipe implements PipeTransform {

  transform(value, key: string, term: string): any {
    let keys = key.split(' ');

    return value.filter((item) => {

      let res = true;
      keys.forEach(k => {
        if (!item.hasOwnProperty(k)) res = false;
      })
      if (res) {
        if (term) {
          let result = false;
          keys.forEach(k => {
            if (item[k] && term) {
              let tmpItem = Diacritics.removeDiacritics(item[k].toLowerCase());
              let tmpTerm = Diacritics.removeDiacritics(term.toLowerCase());
              if (tmpItem && tmpTerm && tmpItem.indexOf(tmpTerm) > -1) {
                result = true;
              }
            }
          });
          return result;

        } else {
          return true;
        }
      } else {
        return false;
      }
    });
  }

}



