import { Component, OnInit, ViewChild } from '@angular/core';
import { Reservation } from '../../../model/location/reservation';
import { MyReservationsDetailComponent } from '../my-reservations-detail/my-reservations-detail.component';

@Component({
  selector: 'app-my-reservations-wrapper',
  templateUrl: './my-reservations-wrapper.component.html',
  styleUrls: ['./my-reservations-wrapper.component.css']
})
export class MyReservationsWrapperComponent implements OnInit {
  selectedReservation: Reservation;
  @ViewChild(MyReservationsDetailComponent) myReservationsDetail: MyReservationsDetailComponent;
  constructor() { }

  ngOnInit() {
  }

  onSelectReservation(sr: Reservation) {
    this.selectedReservation = sr;
    setTimeout(() => {
      this.myReservationsDetail.onSelectSearchResource();
    }, 100);

  }

}



