import { Attendee } from './sub/attendee';
import { Resource } from './resource';
import * as moment from 'moment';
import { CharacComplementType } from './sub/charac-complement-type';

export class Reservation {

    public id: number;
    public tenantId: number;
    public id_resource: number;
    public start_date: number;
    public end_date: number;
    public user_ref: string;
    public charac_complement_type: CharacComplementType[]=[];
    public real_start_date: number;
    public real_end_date: number;
    public state: string;
    public gateway_state: string;
    public create_date: number;
    public last_state_modification_date: number;
    public attendees: Attendee[]=[];
    public resource: Resource;
    public start_date_string: string;
    public start_time_string: string;
    // public start_date_time_string: string;
    public end_date_string: string;
    public end_time_string: string;
    // public end_date_time_string: string;
    startButtonVisible:boolean;
    closeButtonVisible:boolean;
    stopButtonVisible:boolean;
    deleteButtonVisible:boolean;
    
    constructor(obj?: any) {
        this.id = obj && obj.id || null;
        this.tenantId = obj && obj.tenantId || null;
        this.id_resource = (obj && obj.id_resource) || null;
        this.start_date = obj && obj.start_date || null;
        this.start_date_string = obj && this.dateToDateString(obj.start_date) || null;
        this.start_time_string = obj && this.dateToTimeString(obj.start_date) || null;
        this.end_date = obj && obj.end_date || null;
        this.end_date_string = obj && this.dateToDateString(obj.end_date) || null;
        this.end_time_string = obj && this.dateToTimeString(obj.end_date) || null;
        this.user_ref = obj && obj.user_ref || null;
        this.real_start_date = obj && obj.real_start_date || null;
        this.real_end_date = obj && obj.real_end_date || null;
        this.state = obj && obj.state || null;
        this.gateway_state = obj && obj.gateway_state || null;
        this.create_date = obj && obj.create_date || null;
        this.last_state_modification_date = obj && obj.last_state_modification_date || null;

        if (obj && obj.charac_complement_type) {
            obj && obj.charac_complement_type.forEach(child => {
                this.charac_complement_type.push(child);
            });
        }
        if (obj && obj.attendees) {
            obj && obj.attendees.forEach(child => {
                this.attendees.push(child);
            });
        }
    }
    dateToDateString(epoch:number):string{
        var d = new Date(0); // The 0 there is the key, which sets the date to the epoch
        d.setUTCMilliseconds(epoch);
        var momentObj = moment(d);
        return momentObj.format("YYYY-MM-DD");
    }

    dateToTimeString(epoch:number):string{
        var d = new Date(0); // The 0 there is the key, which sets the date to the epoch
        d.setUTCMilliseconds(epoch);
        var momentObj = moment(d);
        return momentObj.format("HH:mm");
    }



}


