export class LocationType {

    public id: number;
    public label: string;
    // public id_place_type_parent: string;
    public id_place_type_parent: number;
    public value: string;
    public children: LocationType[];

    constructor(obj?: any) {
        this.id = obj && obj.id || null;
        this.label = obj && obj.label || null;
        // this.id_place_type_parent = obj && obj.id_place_type_parent || null;
        this.id_place_type_parent = (obj && obj.id_place_type_parent) || null;
        this.value = obj && obj.label || null;

        if (obj && obj.children) {
            obj && obj.children.forEach(child => {
                this.children.push(child);
            });
        }
    }

}


