import { AppConfigurationService } from "../../../modules/core";

export class Charac {

    public id: number;
    public pictogram: string;
    public pictogramFullPath: string;
    public value: string;
    public type: string;
    public label: string;
    public mandatory: string;
    public id_resource: number;
    public id_resource_type: number;
    public id_charactype_resource: number;

    constructor(obj?: any) {
        this.id = obj && obj.id || null;
        this.pictogram = obj && obj.pictogram || null;
        this.value = obj && obj.value || null;
        this.type = obj && obj.type || null;
        this.label = obj && obj.label || null;
        this.mandatory = obj && obj.mandatory || null;
        this.id_resource = obj && obj.id_resource || null;
        this.id_resource_type = obj && obj.id_resource_type || null;
        this.id_charactype_resource = obj && obj.id_charactype_resource || null;
        this.pictogramFullPath = obj && obj.pictogram || null;
    }

}


