import { Component, ViewChild, ElementRef, Input, OnInit, SimpleChanges, OnChanges } from '@angular/core';
import { HttpService, AppConfigurationService } from 'app/modules/core';

@Component({
    selector: 'img-secure',
    templateUrl: './img-secure.component.html',
    styleUrls: ['./img-secure.component.css']
})
export class ImgSecureComponent implements OnInit {
    @Input() src;

    @ViewChild('imgRef') img: ElementRef;

    constructor(private httpService: HttpService,
        private appConfigurationService: AppConfigurationService) { }

    ngOnInit() {
    }

    ngOnChanges(changes: SimpleChanges) {
        this.httpService.get(this.appConfigurationService.urlAPIV1 + this.src).subscribe(
            res => {
                let data = res.json();
                let type = data.file_name.split('.')[1];
                this.img.nativeElement.src = 'data:image/' + type + ';base64,' + data.data;
            }
        )


    }

}



