import { Injectable } from '@angular/core';
import { Http, XHRBackend, RequestOptionsArgs, RequestOptions, Headers, Response } from '@angular/http';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Observable } from 'rxjs/Observable';
import { Router } from '@angular/router';
import { PathService } from 'app/modules/core/services/path.service';
import { ErrorObservable } from 'rxjs/observable/ErrorObservable';
import { InfoLocalStorageService } from 'app/modules/localStorage';
import { Office365LocalStorageService } from 'app/modules/localStorage/services/office365LocalStorage.service';
import 'rxjs/add/observable/throw';
import { AlertService } from './alert.service';
import { AppConfigurationService } from '..';
import { User365 } from "app/modules/authentication/modules/office365/models/user365.model";

@Injectable()
export class HttpService extends Http {

    static instanceCount = 0;
    private unLocked = new BehaviorSubject(true);

    private authRoute = '';
    private buffer: any[] = [];

    constructor(
        backend: XHRBackend,
        options: RequestOptions,
        private router: Router,
        private infoLocalStorageService: InfoLocalStorageService,
        private office365LocalStorageService: Office365LocalStorageService,
        private alertService: AlertService,
        private pathService:PathService
        ) {
        super(backend, options);
        HttpService.instanceCount++;
        // console.log('Started constructor of HttpService (instanceCount=' + HttpService.instanceCount + ')');
        this.authRoute = this.pathService.getApiPath('AUTH-ROUTE');
        // console.log('Done constructor of HttpService');
    }

    public logout(): void {
        //localStorage.clear();
        //this.router.navigate(['login']);
        //window.location.href = this.appConfigurationService.urlV2 + 'login';
        this.pathService.logout();
    }

    public async _serverError(err: any): Promise<any> {
        if (err instanceof Response) {
            const error = await err.json();
            if (err.status === 404) {
                return Observable.throw('BACKEND-ERROR-404');
            } else if (err.status === 401 && error.error) {
                return Observable.throw(error.error);
            }
        }
        return Observable.throw('BACKEND-ERROR');
    }

    public setupHeader(options?: RequestOptionsArgs, userToken?:string): RequestOptionsArgs {

        if (!options) {
            const headers = new Headers();
            options = new RequestOptions({ headers });
        } else if (options && !options.headers){
            options.headers = new Headers();
        }
        // todo get authenticationToken
        const authenticationToken = this.infoLocalStorageService.token;

        if (authenticationToken) {
            options.headers.set('Access-Token', authenticationToken);
        }
        if (userToken) {
            options.headers.set('User-Token', userToken);
        }

        options.headers.append('Content-Type', 'application/json');
        options.headers.append('sender', 'fow');
        return options;
    }

    public get(url: string, options?: RequestOptionsArgs, userToken?: string): Observable<Response> {
        return super.get(url, this.setupHeader(options, userToken)).catch(this.catchAuthError());
    }

    public delete(url: string, options?: RequestOptionsArgs, userToken?: string): Observable<Response> {
        return super.delete(url, this.setupHeader(options, userToken)).catch(this.catchAuthError());
    }

    public post(url: string, body: any, options?: RequestOptionsArgs, userToken?: string): Observable<Response> {
        
        return super.post(url, body, this.setupHeader(options, userToken)).catch(this.catchAuthError());
    }

    public put(url: string, body: any, options?: RequestOptionsArgs, userToken?: string): Observable<Response> {
        return super.put(url, body, this.setupHeader(options, userToken)).catch(this.catchAuthError());
    }

    private catchAuthError(): (Response) => ErrorObservable {
        // we have to pass HttpService's own instance here as `self`
        return (res: Response) => {
            if (res.status === 401) {
                // console.log('401 Unauthorized');
            } else if (res.status === 400 && res.json().error) {
                return Observable.throw(res.json().error);
            }
            return Observable.throw(res);
        };
    }

    public async requestZenOfficeToken(): Promise<void> {
        
        // console.log("request zenoffice token");
        const user: User365 = this.office365LocalStorageService.userOffice365;
        const token = this.office365LocalStorageService.tokenOffice365;
        const dataToSend = '\"' + token + '\"';
        const response = await this.post(this.pathService.getApiPath('LOGIN_OAUTH') + user.mail, dataToSend).toPromise();
        this.saveTokenInStorage(response);
    }

    public async refreshZenOfficeToken(): Promise<void> {
        
        // console.log("refresh zenoffice token");

        if(this.infoLocalStorageService.rememberMeToken){
            const dataToSend = {
                login: '', password: '', ask_remember_me: true, remember_me_token: this.infoLocalStorageService.rememberMeToken
            }; 

            const response = await this.post(this.pathService.getApiPath('LOGIN'), dataToSend).toPromise();
            this.saveTokenInStorage(response);
        }
        else{
            this.requestZenOfficeToken();
        }
    }

    public saveTokenInStorage(resp: any): void {
        const data = JSON.parse(resp._body);
        if (data) {
            if(data.token){
                this.infoLocalStorageService.token = data.token;
            }

            if(data.remember_me_token){
                this.infoLocalStorageService.rememberMeToken = data.remember_me_token;
            }
        }
    }
}



