import { Injectable } from '@angular/core';
import { Http, Headers, Request, Response } from '@angular/http';
import { Observable, Subject, BehaviorSubject, Subscription } from 'rxjs';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

import { Serializable } from '../../model/utils';
import { Reservation } from '../../model/location/reservation';
import { HttpService, PathService } from 'app/modules/core';
import { Office365LocalStorageService } from '../../modules/localStorage/services/office365LocalStorage.service';

@Injectable()
export class ReservationService {

    constructor(private http: HttpService,
        private pathService: PathService,
        private office365LocalStorageService: Office365LocalStorageService) { }

    // get list of reservations
    getReservations() {

        let data: Reservation[] = [];

        return this.http.get(this.pathService.getApiPath('RESERVATION-SEARCH'))
            .map((res: Response) => {
                let list = res.json();

                list.forEach(element => {
                    let tmp = new Serializable(element)
                    let tmp2 = new Reservation(tmp)
                    data.push(tmp2);
                });

                return data;
            })

    }

    start(id) {

        return this.http.put(this.pathService.getApiPath('RESERVATION') + '/' + id + '/start', null)
            .map((res: Response) => {
                return res;
            });
    }

    delete(id) {
        let userToken = this.office365LocalStorageService.tokenOffice365;

        return this.http.delete(this.pathService.getApiPath('RESERVATION') + '/' + id, null, userToken)
            .map((res: Response) => {
                return res;
            });
    }

    createOrUpdate(obj: Reservation) {

        let tmp: any = obj;

        // tmp.create_date = tmp.create_date.getTime();
        // delete tmp.image_ids;
        // delete tmp.bread_crumb;
        // if (obj && !obj.id) {
        //     delete obj.id;
        // }
        //tmp.charac_complement_type=null;
        //tmp.attendees=null;

        let userToken = this.office365LocalStorageService.tokenOffice365;

        let dataToSend = JSON.stringify(tmp, this.replacer);

        if ((obj && obj.id)) {
            // update
            return this.http.put(this.pathService.getApiPath('RESERVATION') + '/' + obj.id, dataToSend, null, userToken)
                .map((res: Response) => {
                    return res.json();
                });
        } else {
            // create
            return this.http.post(this.pathService.getApiPath('RESERVATION'), dataToSend, null, userToken)
                .map((res: Response) => {
                    return res.json();
                });
        }
    }
    replacer(key, value) {
        if (key == "start_date_string") return undefined;
        else if (key == "start_time_string") return undefined;
        else if (key == "end_date_string") return undefined;
        else if (key == "end_time_string") return undefined;
        else if (key == "tenant_id") return undefined;
        else if (key == "tenantId") return undefined;
        else if (key == "resource") return undefined;
        else if (key == "startButtonVisible") return undefined;
        else if (key == "closeButtonVisible") return undefined;
        else if (key == "stopButtonVisible") return undefined;
        else if (key == "deleteButtonVisible") return undefined;
        else return value;
    }
    // updateState(id: number, info: any) {

    //     let dataToSend = JSON.stringify(info);

    //     return this.http.put(this.pathService.getApiPath('REPORT-STATE') + '/' + id, dataToSend)
    //         .map((res: Response) => {
    //             return res;
    //         });
    // }

    // getIncidentTypeList() {
    //     let data: Incident[] = [];
    //     data.push(new Incident({ 'id': '', 'label': '' }))

    //     return this.http.get(this.pathService.getApiPath('REPORT-INCIDENT-ALL'))
    //         .map((res: Response) => {
    //             let list = res.json();

    //             list.forEach(element => {
    //                 let tmp = new Serializable(element)
    //                 let tmp2 = new Incident(tmp)
    //                 data.push(tmp2);
    //             });

    //             return data;
    //         })
    // }


    // uploadImg(data) {
    //     let tmp: any = data;
    //     tmp.data = tmp.data.split(',')[1];
    //     tmp.create_date = tmp.create_date.getTime();
    //     let dataToSend = JSON.stringify(tmp);

    //     return this.http.post(this.pathService.getApiPath('REPORT-IMAGE'), dataToSend)
    //         .map((res: Response) => {
    //             return res;
    //         });
    // }

}



