import { Incident } from './sub/incident';
import { Place } from '../location/place';

export class Report {

    public id: number;
    public create_date: Date;
    public description: string;
    public report_state: string;
    public incident_type: Incident = new Incident();
    public reporter: string;
    public location_id: number;
    public location_type: string;
    public location_label: number;
    public location_type_label: string;
    public invalid_reason: string;
    public image_ids: number[] = [];
    public location_parents: Place[] = [];
    public bread_crumb: string = '';

    constructor(obj?: any) {
        this.id = obj && obj.id || null;

        if (obj && obj.create_date && typeof obj.create_date === 'number') {
            this.create_date = obj && new Date(obj.create_date) || null;
        } else {
            this.create_date = obj && obj.create_date || null;
        }

        this.report_state = obj && obj.report_state || null;
        this.description = obj && obj.description || null;

        if (obj && obj.incident_type) {
            this.incident_type = new Incident(obj.incident_type);
        }


        this.reporter = obj && obj.reporter || '';
        this.location_type = obj && obj.location_type || null;
        this.location_id = obj && obj.location_id || null;
        this.location_label = obj && obj.location_label || null;
        this.location_type_label = obj && obj.location_type_label || null;
        this.invalid_reason = obj && obj.invalid_reason || '';

        if (obj && obj.location_parents) {
            obj.location_parents.forEach(p => {
                if (p.label) this.bread_crumb += p.label + ' > ';
                this.location_parents.push(new Place(p));
            })
        }

        if (obj && obj.image_ids) {
            obj.image_ids.forEach(i => {
                this.image_ids.push(i)
            })
        }
    }

}



