
/*
    a class that can be set from json
*/

export class Serializable {

    constructor(json) {
        for (let propName in json) {
            this[propName] = json[propName];
        }
        return this;
    };

}

export class Diacritics {

    static removeDiacritics(value) {
        return value
            .replace(/á/g, 'a')            
            .replace(/é/g, 'e')
            .replace(/í/g, 'i')
            .replace(/ó/g, 'o')
            .replace(/ú/g, 'u');
    }
}


