import { Injectable } from '@angular/core';
import { Http, Headers, Request, Response, RequestOptions, RequestOptionsArgs } from '@angular/http';
import { Observable, Subject, BehaviorSubject, Subscription } from 'rxjs';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

import { Serializable } from '../../model/utils';
import { Resource } from '../../model/location/resource';
import { HttpService, PathService, AppConfigurationService } from 'app/modules/core';
import { SearchResource } from '../../model/location/searchResource';
import { Charac } from '../../model/location/sub/charac';
import { CharacTypeComplementType } from '../../model/location/sub/charactype-complement-type';
import { Picture } from '../../model/location/sub/picture';
import * as moment from 'moment';
import { CalendarItem } from 'app/model/location/calendar-item';

@Injectable()
export class ResourceService {

    constructor(private http: HttpService,
        private pathService: PathService,
        private appConfigurationService: AppConfigurationService) { }

    // get resource by id
    getResourceById(id: number) {

        let data: Resource;

        return this.http.get(this.pathService.getApiPath('RESOURCE-SEARCH') + '/' + id)
            .map((res: Response) => {
                let resource = res.json();
                let data = new Resource(resource)
                data.charac.forEach(car => {
                    car.pictogramFullPath = this.appConfigurationService.pictoAddress + car.pictogram;
                });
                return data;
            })
    }
    getCaracTypeByResourceType(id: number) {

        let data: Charac[] = [];

        return this.http.get(this.pathService.getApiPath('RESOURCE-TYPE') + '/' + id + "/characTypeResourceType")
            .map((res: Response) => {
                let list = res.json();

                list.forEach(element => {
                    let tmp = new Serializable(element)


                    let tmp2 = new Charac(tmp)
                    tmp2.pictogramFullPath = this.appConfigurationService.pictoAddress + tmp2.pictogramFullPath;
                    data.push(tmp2);
                });

                return data;
            })
    }

    getCaracTypeByComplementType(id: number) {

        let data: CharacTypeComplementType[] = [];

        return this.http.get(this.pathService.getApiPath('COMPLEMENT-TYPE') + '/' + id + "/characTypeComplementType")
            .map((res: Response) => {
                let list = res.json();

                list.forEach(element => {
                    let tmp = new Serializable(element)


                    let tmp2 = new CharacTypeComplementType(tmp)
                    data.push(tmp2);
                });

                return data;
            })
    }
    searchResource(searchedText: string, startDate?: number, endDate?: number, pageType?: number, idAncestor?: number) {
        let options: RequestOptionsArgs = null;
        const headers = new Headers();
        options = new RequestOptions({ headers });
        options.headers.append('search', searchedText);
        let searchURI = this.pathService.getApiPath('RESOURCE');
                
        if(startDate && endDate){
            searchURI +=
            "?startDate=" + startDate
            + "&endDate=" + endDate;
        }
        else
            searchURI += "?";

        if (pageType === 0) {
            //SearchPage
            searchURI = searchURI + "&visibleInSearchPage=true";
        }
        else if (pageType === 1) {
            //AvailabilityPage
            searchURI = searchURI + "&visibleInAvailabilityPage=true";
        }

        
        
        
        if (idAncestor) {
            searchURI = searchURI + "&place=" + idAncestor;
        }

        let data: SearchResource[] = [];
        return this.http.get(searchURI, options)
            .map((res: Response) => {

                let list = res.json();

                list.forEach(element => {
                    let tmp = new Serializable(element)
                    let tmp2 = new SearchResource(tmp)

                    tmp2.resource.charac.forEach(car => {
                        car.pictogramFullPath = this.appConfigurationService.pictoAddress + car.pictogram;
                    });
                    data.push(tmp2);
                });
                var sortedData: SearchResource[] =
                    data.sort((n1, n2) => {
                        // Sort by availability
                        if (n1.isAvailale < n2.isAvailale) {
                            return 1;
                        }
                        if (n1.isAvailale > n2.isAvailale) {
                            return -1;
                        }
                        // Same availability => sort by name
                        if (n1.resource.label > n2.resource.label) {
                            return 1;
                        }

                        if (n1.resource.label < n2.resource.label) {
                            return -1;
                        }

                        return 0;
                    });
                return sortedData;

            });
    }


    getPictures(id: number) {
        let data: Picture[] = [];

        return this.http.get(this.pathService.getApiPath('RESOURCE-PICTURES') + id)
            .map((res: Response) => {
                let list = res.json();

                list.forEach(element => {
                    let tmp = new Serializable(element)
                    let tmp2 = new Picture(tmp)
                    data.push(tmp2);
                });

                return data;
            })
    }


    getCalendar(id: number) {
        let data: CalendarItem[] = [];
        let start: number = moment().add(-15, 'days').unix() * 1000;
        let end: number = moment().add(2, 'months').unix() * 1000;

        return this.http.get(this.pathService.getApiPath('RESOURCE')
            + '/' + id
            + '/calendar'
            + '?startDate=' + start
            + '&endDate=' + end)
            .map((res: Response) => {
                let list = res.json();

                list.appointments.forEach(element => {
                    let tmp = new CalendarItem();
                    tmp.title = element.title;

                    var dStart = new Date(0); // The 0 there is the key, which sets the date to the epoch
                    dStart.setUTCMilliseconds(element.start_date);
                    var momentObjStart = moment(dStart);
                    tmp.start = momentObjStart.toDate();

                    var dEnd = new Date(0); // The 0 there is the key, which sets the date to the epoch
                    dEnd.setUTCMilliseconds(element.end_date);
                    var momentObjEnd = moment(dEnd);
                    tmp.end = momentObjEnd.toDate();

                    tmp.description = '?';

                    data.push(tmp);
                });

                return data;
            })
    }
}



