import { Injectable, OnDestroy } from "@angular/core";
import { Response } from '@angular/http';
import { HttpService, PathService, AppConfigurationService } from 'app/modules/core';
import { InfoLocalStorageService } from 'app/modules/localStorage';
import { Office365LocalStorageService } from 'app/modules/localStorage/services/office365LocalStorage.service';
import { User365 } from 'app/modules/authentication/modules/office365/models/user365.model';
import { Router } from '@angular/router';

@Injectable()
export class ZenOfficeAuth implements OnDestroy {

    constructor(
        private httpService: HttpService,
        private infoLocalStorageService: InfoLocalStorageService,
        private office365LocalStorageService: Office365LocalStorageService,
        private router: Router,
        private appConfigurationService: AppConfigurationService,
        private pathService:PathService) {

        this.createRefreshTokenTimer();
    }

    ngOnDestroy() {
        clearInterval(this.interval);
    }

    private interval;
    private refreshTokenTimeoutInMinute : number = 10;

    public async login(login: string, password: string, askRemember: boolean): Promise<void> {
        // todo : implementation
        const dataToSend = {
            login: login, password: password, ask_remember_me: askRemember, remember_me_token: ''
        };
        const response = await this.httpService.post(this.pathService.getApiPath('LOGIN'), dataToSend).toPromise();
        this.httpService.saveTokenInStorage(response);
    }

    public async loginOAuth(): Promise<void> {
        await this.httpService.requestZenOfficeToken();
        window.location.href = this.appConfigurationService.urlAccueil;
    }

    public async isAuthenticated(): Promise<boolean> {

        const isTokenFilled: boolean = (this.infoLocalStorageService.token) ? true : false;

        if (isTokenFilled) {

            let isTokenValid = await this.isTokenValid();

            if (isTokenValid) {
                return true;
            } else {
                await this.httpService.refreshZenOfficeToken();
                // console.log("token expire and relogin");
                return await this.isTokenValid();
            }
        } else {
            return false;
        }
    }

    private async createRefreshTokenTimer(){
        this.interval = setInterval( () => { 
            let isTokenFilled: boolean = (this.infoLocalStorageService.token) ? true : false;

            if(isTokenFilled){
                this.httpService.refreshZenOfficeToken();
            }
        }, this.refreshTokenTimeoutInMinute * 60 *1000);
    }

    private async isTokenValid() : Promise<boolean>{

        const response = await this.httpService.get(this.pathService.getApiPath('RESERVATION')).toPromise();

        if (response.ok) {
            return true;
        }
        else{
            return false;
        }
    }
}



