import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { GraphHelperService } from './graphHelper.service';
import { User365 } from '../models/user365.model';
import { Office365LocalStorageService } from 'app/modules/localStorage/services/office365LocalStorage.service';

@Injectable()
export class Office365Service {

    constructor(
        private httpService: Http,
        private graphHelperService: GraphHelperService,
        private office365LocalStorageService: Office365LocalStorageService) {  }

    public async isAuthenticated(): Promise<boolean> {
        this.office365LocalStorageService.userOffice365 = undefined;
        const token = this.office365LocalStorageService.tokenOffice365;
        if (token) {
            const user = await this.graphHelperService.processAuth();
            // console.log('User Office 365', user);
            if(user) {
                return true;
            } else {
                return false;
            }
        } else {
            return false;
        }
    }

    public async login(): Promise<void> {
        await this.graphHelperService.login();
    }
}



