import { Injectable } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ShowCalendarComponent } from './show-calendar.component';

@Injectable()
export class ShowCalendarService {

  constructor(private modalService: NgbModal) { }

  public show(
    id:number,
    label:string): Promise<boolean> {
    const modalRef = this.modalService.open(ShowCalendarComponent, { windowClass: 'modal-dialog-centered', size: 'lg' });
    modalRef.componentInstance.id = id;
    modalRef.componentInstance.label = label;

    return modalRef.result;
  }

}



