import { Injectable } from '@angular/core';
import * as MicrosoftGraph from '@microsoft/microsoft-graph-client/lib/src/index';
import { Office365LocalStorageService } from 'app/modules/localStorage/services/office365LocalStorage.service';
import { ZenOfficeAuth } from 'app/modules/authentication/modules/zenoffice/services/zenoffice-auth.service';
import { User365 } from 'app/modules/authentication/modules/office365/models/user365.model';
import * as Office365Transformer from '../transformers/office365.transformer';
import { InfoLocalStorageService } from 'app/modules/localStorage';
import { environment } from 'environments/environment';
import { AppConfigurationService, PathService } from 'app/modules/core';
import { TenantConfigurationService } from 'app/modules/core';

// inclued in index.html
declare var Msal: any;

@Injectable()
export class GraphHelperService {
    private clientApplication;
    public graphClient: any;
    public logger: any;
    public authToken: string;
    // This is a special key from a project registered in Azure.
    //public clientID: string = '75819db6-3601-4c59-9cec-575d678f71a5';
    //public clientID: string;// = this.tenantConfigurationService.MicrosoftApplicationId;
    public clientID: string;//= this.appConfigurationService.clientID;
    // public clientID: string = '90b9dbe2-5471-4d3a-858d-d6e62cf4d02e';
    public graphScopes: string[] = ['user.read', 'Calendars.ReadWrite'];

    constructor(
        private office365LocalStorageService: Office365LocalStorageService,
        private infoLocalStorageService: InfoLocalStorageService,
        private zenOfficeService: ZenOfficeAuth,
        private appConfigurationService: AppConfigurationService,
        private pathService: PathService,
        private tenantConfigurationService: TenantConfigurationService) {
        //this.clientID = this.tenantConfigurationService.MicrosoftApplicationId;
        //if (this.clientID === undefined) {
        this.clientID = this.appConfigurationService.clientID;
         //}
        this.init();
    }

    public async login(): Promise<void> {
        let idToken;
        const user = this.clientApplication.getUser();
        try {
            idToken = await this.clientApplication.loginRedirect(this.graphScopes);
        } catch (error) {
            window.alert('Error during login:\n');
        }
    }

    public me(): any {
        return this.graphClient.api('/me').get().then((res) => {
            return res;
        }).catch((err) => {
            return null;
        });
    }

    public init(): void {
        if (this.office365LocalStorageService.tokenOffice365) {
            this.authToken = this.office365LocalStorageService.tokenOffice365;
        }

        // tslint:disable-next-line:semicolon
        this.logger = new Msal.Logger((logLevel, message, piiEnabled) => {
            console.log(message);
        }, { level: Msal.LogLevel.Verbose, correlationId: '12345' });

        if (this.office365LocalStorageService.userOffice365) {
            if (this.office365LocalStorageService.userOffice365.mail) {
                console.log('>>> Let s try to call the tenant service now (mail = ' + this.office365LocalStorageService.userOffice365.mail + ')');
                console.log('>>> clientID BEFORE = ' + this.clientID);
                
                //var toto = this.tenantConfigurationService;
                // Only now can we load the tenant configuration
                //CONFIGURATION TENANT
                this.tenantConfigurationService.load().then(data => {
                    console.log('Success callback', data);
                }).catch(err => {
                    console.log('Error callback', err);
                })

            }
            else {
                console.log('>>> user exists but we don t have the mail info yet');
            }
        }
        else {
            console.log('>>> No use to call the tenant service now : we don t have the login yet');
        }
        
        console.log('>>>> Calling new Msal.UserAgentApplication with clientID = '+ this.clientID)

        this.clientApplication = new Msal.UserAgentApplication(this.clientID, null, async (errorDesc, token, error, tokenType) => {
            // Called after loginRedirect or acquireTokenPopup
            try {
                this.authToken = await this.clientApplication.acquireTokenSilent(this.graphScopes);
                this.office365LocalStorageService.tokenOffice365 = this.authToken;
                await this.processAuth();
                this.infoLocalStorageService.isWaitingForAuthToComplete = false;
                await this.zenOfficeService.loginOAuth();
            } catch (error) {
                /*try {
                    this.authToken = await this.clientApplication.acquireTokenPopup(this.graphScopes);
                } catch (error) {
                    window.alert('Error acquiring the popup:\n' + error);
                }*/
            }
        }, { cacheLocation: 'localStorage', logger: this.logger });
        this.graphClient = MicrosoftGraph.Client.init({
            authProvider: (done) => {
                if (typeof this.authToken === 'undefined') {
                    done({ err: 'No auth token' }, null);
                } else {
                    done(null, this.authToken); // first parameter takes an error if you can't get an access token
                }
            }
        });
    }

    public async processAuth(): Promise<User365> {
        // let the authProvider access the access token
        let user365 = this.office365LocalStorageService.userOffice365;
        console.log('User Office 365', user365);
        if (user365 === null) {

            // Get the profile of the current user.
            let user;
            try {
                user = await this.me();
                console.log('Ask for User Office 365 Info', user365);
                // Save the user to localStorage.
                user365 = Office365Transformer.transformFromOfficeToUserInfo(user);
                this.office365LocalStorageService.userOffice365 = user365;
            } catch (error) {
                console.log('Failed');
                console.log('Error is', error);
            }
        } else {
            user365 = this.office365LocalStorageService.userOffice365;
        }
        return user365;
    }

}



