import { Component, OnInit, Input } from '@angular/core';
import { MapwizeService } from '../../../services/mapwize/mapwize.service';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-show-mapwize',
  templateUrl: './show-mapwize.component.html',
  styleUrls: ['./show-mapwize.component.css']
})
export class ShowMapwizeComponent implements OnInit {

  @Input() venue: number;
  @Input() resource: string;

  constructor(
    private mapwizeService:MapwizeService,
    private activeModal: NgbActiveModal,
  ) { }

  ngOnInit() {
    this.mapwizeService.container="showMapwize";
    this.initMap();
  }

  initMap(){
    this.mapwizeService.mapCreation()
    .then(mapInstance => {

      let venueToFind=this.mapwizeService.venueToFind;
      this.mapwizeService.searchVenueByName(venueToFind).then((venueId)=>{  

        let resourceToFind=this.mapwizeService.resourceToFind;
        this.mapwizeService.searchPlaceByName(resourceToFind).then((place)=>{ 
                    
          mapInstance.centerOnVenue(venueId);  
          mapInstance.on('mapwize:venueenter', (e) => {
            mapInstance.setUniverse(this.mapwizeService.universeId);
            mapInstance.centerOnPlace(place); 
            
            mapInstance.addMarker({
                latitude:  place.marker.latitude,
                longitude: place.marker.longitude,
                floor:  place.floor,
            })
          });
        });
      });
    })
  }

  public decline() {
    this.activeModal.close(false);
  }
  public dismiss() {
    this.activeModal.dismiss();
  }
}



