import { Injectable } from '@angular/core';
import { AppConfigurationService } from 'app/modules/core';
import * as Mapwize from 'mapwize';
import { forEach } from '@angular/router/src/utils/collection';

@Injectable()
export class MapwizeService {

  mapwize:any=this.appConfigService.mapwize;
  apiKey:string=this.mapwize.api_key;
  organizationId:string=this.mapwize.organization_id;
  universeId:string=this.mapwize.universe_id;
  container:string;
  venueId:string;
  map:any;
  mapInstance:any;
  resourceToFind:any;
  venueToFind:any;

  constructor(
    private appConfigService: AppConfigurationService
  ) {
    Mapwize.apiKey(this.apiKey);
  }

  mapCreation(){
    return this.map = Mapwize.map({
      container: this.container
    },
    {
      venueId: this.venueId,
      userPositionControl:false
    }); 
  }

  getVenuesByOrga(){
    return Mapwize.Api.getVenues({
      organizationId: this.organizationId
    })
  }  

  getPlacesByVenue(){
    return Mapwize.Api.getPlaces({
      venueId:this.venueId,
      universeId:this.universeId
    });
  }

  async searchVenueByName(myVenue){
    let venue;
    await this.getVenuesByOrga()
      .then(venues=>{
        for (let i = 0; i < venues.length; i++) {
          if(venues[i].name===myVenue.label){
            this.venueId=venues[i]._id;
            venue=venues[i]._id;
            break;
          }
        }
      });
    return venue;
  }

  async searchPlaceByName(selectedResource){
    let place;
    await this.getPlacesByVenue()
      .then(places=>{
        for (let i = 0; i < places.length; i++) {
          if(places[i].name===selectedResource.resource.identifier){
            place=places[i];
            break;
          }
        }
      });
    return place;
  }

  clearMap(){
    this.mapInstance=null;
    let mapDiv=document.getElementById(this.container);
    mapDiv.innerHTML="";
  }
}



