import { ReservationService } from '../../../services/reservation/reservation.service';
import { ResourceService } from '../../../services/resource/resource.service';
import { Component, OnInit } from '@angular/core';
import { EventEmitter, Input, Output, SimpleChanges, OnChanges } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Resource } from '../../../model/location/resource';
import { SearchResource } from '../../../model/location/searchResource';
import { Charac } from '../../../model/location/sub/charac';
import { PlaceWithChildren } from '../../../model/location/place-with-children';
import { Place } from '../../../model/location/place';
import { ResourceType } from 'app/model/location/sub/resource-type';
import * as moment from 'moment';
import { FilterSearchPipe } from '../../../services/tools/pipe/filter-search.pipe';
@Component({
    selector: 'app-search-page-resources',
    templateUrl: './search-page-resources.component.html',
    styleUrls: ['./search-page-resources.component.css']
})
export class SearchPageResourcesComponent implements OnInit {
    public loading = false;
    searchResourceList: SearchResource[] = [];
    @Input() selectedSearchResource: SearchResource;
    @Output() onSelectSearchResource: EventEmitter<any> = new EventEmitter();
    @Input() selectedStartDate: Date = new Date();
    @Input() selectedEndDate: Date = new Date();
    @Input() selectedSearchText: string = '';
    @Input() selectedResourceType: ResourceType;
    @Input() caracList: Charac[] = [];
    @Input() selectedPrimary: PlaceWithChildren;
    @Input() selectedSecondary: PlaceWithChildren;
    @Input() pageType: number;

    public todaysDate: Date = new Date();

    constructor(private translateService: TranslateService,
        private reservationService: ReservationService,
        private resourceService: ResourceService) { }

    ngOnInit() {
    }
    public loadData(): void {
        var idResource: any;
        var data: any;
        var data2: any;
        data = [0];
        this.searchResourceList = [];
        if (this.selectedSearchResource) {
            idResource = this.selectedSearchResource.resource.id;
        }

        this.loading = true;
        //Get Data
        this.getData()
            .then(r => {
                data = r;
                
                data.forEach(element => {
                    element.resource.capacity = this.deduceCapacity(element.resource);
                });

                //Filter
                this.searchResourceList = this.filterResourceList(data);
                this.loading = false;
                setTimeout(() => {
                    if (idResource) {
                        let selectedRes = this.searchResourceList.find(sr => sr.resource.id === idResource);
                        // Either the previously selected resource is stilll part of the list 
                        // => we display it again
                        // or it's not 
                        // => selected resource is then null => the list is shown and the detail component is hidden
                        this.onSelectSearchResource.emit(selectedRes);
                    }
                }, 100);
            }
            )
            .catch(err => {
                // console.log("err => réservation non trouvée");
                this.loading = false;
            })
    }

    filterResourceList(data: SearchResource[]): SearchResource[] {
        let list: SearchResource[] = [];
        data.forEach(sr => {
            if (this.isResourceTypeOK(sr)
                && this.areCharacOK(sr)) {
                list.push(sr);
            }
        });
        return list;
    }

    isResourceTypeOK(sr: SearchResource): boolean {
        let isOk: boolean = false;
        if (!this.selectedResourceType) {
            isOk = true;
        }
        else {
            isOk = (this.selectedResourceType.id === sr.resource.resource_type.id)
        }
        return isOk;
    }

    areCharacOK(sr: SearchResource): boolean {
        let isOk: boolean = true;

        this.caracList.forEach(car => {
            let CharacRequested: boolean = (
                (car.type === "long_type") && (car.value && (Number(car.value) || 0) != 0)
                || (car.type === "double_type") && (car.value && (Number(car.value) || 0) != 0)
                || (car.type === "string_type") && (car.value && car.value != "")
                || (car.type === "boolean_type") && (car.value && car.value != "false")
            );
            if (CharacRequested) {
                // Does the resource havethe requested characteristic
                let srOfResource = sr.resource.charac.find(r => r.id_charactype_resource === car.id);
                if (!srOfResource) {
                    //requested characterisc not found in Resource => NO GO
                    isOk = false;
                }
                else {
                    switch (car.type) {
                        case "long_type":
                            if ((Number(srOfResource.value) || 0) < (Number(car.value) || 0)) {
                                //Value of characteristic less than value requested
                                isOk = false;
                            }
                            break;
                        case "double_type":
                            if ((Number(srOfResource.value) || 0) < (Number(car.value) || 0)) {
                                //Value of characteristic less than value requested
                                isOk = false;
                            }
                            break;
                        case "string_type":
                            if (srOfResource.value != car.value) {
                                //Value of characteristic != value requested
                                isOk = false;
                            }
                            break;
                        case "boolean_type":
                            if (srOfResource.value != "true") {
                                //Value of characteristic is false
                                isOk = false;
                            }
                            break;
                        default:
                            isOk = true;
                            break;
                    }
                }
            }
        });
        return isOk;
    }

    ancestorId(): number {
        let id: number = null;
        if (this.selectedSecondary) {
            id = this.selectedSecondary.id;
        }
        else {
            if (this.selectedPrimary) {
                id = this.selectedPrimary.id;
            }
        }
        return id;
    }

    checkLocation(pt: Place[], idTarget: number): boolean {
        if (pt.length === 0) {
            return false
        }
        else {
            if (pt[0].id === idTarget) {
                return true;
            }
            else {
                let pt2: Place[] = [];
                pt.forEach(element => {
                    if (element.id != pt[0].id) {
                        pt2.push(element);
                    }
                });
                return this.checkLocation(pt2, idTarget);
            }
        }
    }

    public deduceCapacity(res: Resource): string {
        var retour: string = '';
        res.charac.forEach(car => {
            if (car.label === 'Capacité') {
                retour = car.value;
            }
        });
        if (retour === '') {
            retour = res.resource_type.label.charAt(0).toUpperCase();
        }
        return retour;
    }
    compareFunction(a, b) {
        if (a.start_date > b.start_date) {
            return 1;
        }
        if (a.start_date < b.start_date) {
            return -1;
        }
        return 0;
    }
    getData() {
        // keep this for Promise
        var that = this;        
        let startDate;
        let endDate;
        if (that.selectedStartDate && that.selectedEndDate){
            startDate=that.selectedStartDate.getTime();
            endDate=that.selectedEndDate.getTime();
        }
        else{
            startDate=null;
            endDate=null;
        }
            return new Promise(function (resolve, reject) {
                that.resourceService.searchResource(
                    that.selectedSearchText,
                    startDate,
                    endDate,
                    that.pageType,
                    that.ancestorId()).subscribe(
                        data => {
                            resolve(data);
                            var nextQuarterOfHour = moment(new Date()).add(15, 'minutes').toDate();
                            that.todaysDate= nextQuarterOfHour;
                        
                        },
                        error => {
                            reject();
                        })
            });
        // }
        // else 
        // { 
        //     return new Promise(function (resolve, reject) {
        //     that.resourceService.searchResource(
        //         that.selectedSearchText,

        //         that.pageType,
        //         that.ancestorId()).subscribe(
        //             data => {
        //                 resolve(data);
        //             },
        //             error => {
        //                 reject();
        //             })
        //     });

        // }
       
    }

    doAction(event, searchResource) {
        event.stopPropagation()
    }

    searchResourceSelected(res: SearchResource) {
        this.selectedSearchResource = res;
        this.onSelectSearchResource.emit(this.selectedSearchResource);
    }

    triggerSearch() {
        this.loadData();        
    };
    deselectResource() {
        this.selectedSearchResource = null;
        this.onSelectSearchResource.emit(this.selectedSearchResource);
    }
}



