import { Component, OnInit, Output, EventEmitter, ViewChild } from '@angular/core';
import { SearchResource } from '../../../model/location/searchResource';
import { ResourceTypeSelectorComponent } from '../../search-page/resource-type-selector/resource-type-selector.component';
import { PlaceSelectorComponent } from 'app/view/search-page/place-selector/place-selector.component';
import { ResourceType } from 'app/model/location/sub/resource-type';
import { Charac } from '../../../model/location/sub/charac';
import { PlaceWithChildren } from '../../../model/location/place-with-children';
import { ProcessService } from 'app/services/process/process.service';
import { SearchPageDetailComponent } from '../../search-page/search-page-detail/search-page-detail.component';
import { SearchPageResourcesComponent } from '../../search-page/search-page-resources/search-page-resources.component';


@Component({
  selector: 'app-availabilities-wrapper',
  templateUrl: './availabilities-wrapper.component.html',
  styleUrls: ['./availabilities-wrapper.component.css']
})
export class AvailabilitiesWrapperComponent implements OnInit {


  selectedSearchResource: SearchResource;
  selectedSearchText: string = '';
  selectedResourceType: ResourceType = null;
  caracList: Charac[] = [];
  selectedPrimary: PlaceWithChildren = null;
  selectedSecondary: PlaceWithChildren = null;


  selectedSearchTextHasChanged = true;
  selectedResourceTypeHasChanged = true;
  caracListHasChanged = true;
  selectedPrimaryHasChanged = true;
  selectedSecondaryHasChanged = true;

  searchHasBeenTriggered: boolean = false;
  @ViewChild(PlaceSelectorComponent) placeSelectorComponent: PlaceSelectorComponent;
  @ViewChild(ResourceTypeSelectorComponent) resourceTypeSelectorComponent: ResourceTypeSelectorComponent;
  @ViewChild(SearchPageResourcesComponent) searchPageResources: SearchPageResourcesComponent;
  @ViewChild(SearchPageDetailComponent) searchPageDetail: SearchPageDetailComponent;
  
  constructor() {
 
  }

  ngOnInit() {}
  onSelectSearchResource(sr: SearchResource) {
    this.selectedSearchResource = sr;
    //It takes time for SearchPageRessources to be aware that selectedPrimary has changed
    setTimeout(() => {
      this.searchPageDetail.onSelectSearchResource();
    }, 100);

  }
  onSelectResourceType(resourceType: ResourceType) {
    this.selectedResourceType = resourceType;
    this.selectedResourceTypeHasChanged = true;
    if (this.searchHasBeenTriggered) {
      setTimeout(() => {
        this.doSearch();
      }, 500);
    }

    this.placeSelectorComponent.filterPlacesByResourceType(this.selectedResourceType);
  }

  onSelectCaracList(c: Charac[]) {
    this.caracList = c;
    this.caracListHasChanged = true;
    if (this.searchHasBeenTriggered) {
      setTimeout(() => {
        this.doSearch();
      }, 500);
    }
  }

  onSelectPrimary(primaryPlace: PlaceWithChildren) {
    this.selectedPrimary = primaryPlace;
    this.selectedPrimaryHasChanged = true;
    if (this.searchHasBeenTriggered) {
      setTimeout(() => {
        this.doSearch();
      }, 500);
    }

    this.resourceTypeSelectorComponent.filterResourceTypesByPlace(this.selectedPrimary, this.selectedSecondary);
  }

  onSelectSecondary(secondaryPlace: PlaceWithChildren) {
    this.selectedSecondary = secondaryPlace;
    this.selectedSecondaryHasChanged = true;
    if (this.searchHasBeenTriggered) {
      setTimeout(() => {
        this.doSearch();
      }, 500);
    }

    this.resourceTypeSelectorComponent.filterResourceTypesByPlace(this.selectedPrimary, this.selectedSecondary);
  }

  onSelectSearchText(st: string) {
    this.selectedSearchText = st;
  }

  onEnterKeyPressed() {
    this.selectedSearchTextHasChanged = true;
    this.doSearch();
  }

  search() {
    // Force search
    this.selectedSearchTextHasChanged = true;
    this.doSearch();
  }

  doSearch() {
    // Launch search
    if (
      
      this.selectedSearchTextHasChanged ||
      this.selectedResourceTypeHasChanged ||
      this.caracListHasChanged ||
      this.selectedPrimaryHasChanged ||
      this.selectedSecondaryHasChanged
    ) {
      this.searchPageResources.triggerSearch();
      this.searchHasBeenTriggered = true;

      
      this.selectedSearchTextHasChanged = false;
      this.selectedResourceTypeHasChanged = false;
      this.caracListHasChanged = false;
      this.selectedPrimaryHasChanged = false;
      this.selectedSecondaryHasChanged = false;
    }
  }
}



