import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { LocationType } from '../../../model/location/location-type';
import { Observable } from '../../../../../node_modules/rxjs';
import { TreeService } from '../../../services/tree/tree.service';
import { TranslateService } from '../../../../../node_modules/@ngx-translate/core';
import { ResourceService } from '../../../services/resource/resource.service';
import { Charac } from '../../../model/location/sub/charac';
import { ResourceType } from 'app/model/location/sub/resource-type';
import { PlaceWithChildren } from 'app/model/location/place-with-children';

@Component({
    selector: 'app-resource-type-selector',
    templateUrl: './resource-type-selector.component.html',
    styleUrls: ['./resource-type-selector.component.css']
})
export class ResourceTypeSelectorComponent implements OnInit {

    @Input() selectedResourceType: ResourceType;
    @Input() where: string;
    @Input() selectedPrimary: PlaceWithChildren;
    @Input() selectedSecondary: PlaceWithChildren;
    @Input() caracList: Charac[] = [];
    @Output() onSelectResourceType = new EventEmitter();
    @Output() onSelectCaracList = new EventEmitter();

    selectedResourceTypeLabel: string;
    resourceTypeList: ResourceType[] = [];
    fullResourceTypeList: ResourceType[] = [];

    constructor(private treeService: TreeService, private resourceService: ResourceService, private translateService: TranslateService) { }

    ngOnInit() {
        Observable.forkJoin(
            this.treeService.getResourceTypeList())
            .subscribe(data => {
                if (data) {

                    // this.resourceTypeList = [...data[0]]
                    data[0].forEach(resource => {
                        if (resource.visible_in_search_page && this.where === "reservation") 
                        {
                            this.resourceTypeList.push(resource);
                            this.fullResourceTypeList.push(resource);
                        }
                        else if (resource.visible_in_availability_page && this.where === "availabilities")
                        {
                            this.resourceTypeList.push(resource);
                            this.fullResourceTypeList.push(resource);
                        }                        
                    })
                    //console.log(this.resourceTypeList);

                    // this.resourceTypeList = data[0];
                    // this.fullResourceTypeList = data[0];
                }
            });
    }

    changeResourceType(prim) {
        if (this.resourceTypeList.find(con => con.label == prim)) {
            this.selectedResourceType = this.resourceTypeList.find(con => con.label == prim);

            Observable.forkJoin(
                this.resourceService.getCaracTypeByResourceType(
                    this.resourceTypeList.find(con => con.label == prim).id)
            )
                .subscribe(data => {
                    if (data) {
                        this.caracList = data[0];
                        this.caracChanged();

                    }
                    else
                        this.caracList = [];
                    this.caracChanged();
                });
        }
        else {
            this.caracList = [];
            this.caracChanged();
            this.selectedResourceType = null;
        }
        //It takes time for SearchPageRessources to be aware that selectedResource has changed
        setTimeout(() => {
            // console.log(this.selectedResourceType)
            this.onSelectResourceType.emit(this.selectedResourceType);
            //this.caracChanged();
        }, 10);
    }

    public filterResourceTypesByPlace(primaryPlace: PlaceWithChildren, secondaryPlace: PlaceWithChildren) {

        if (this.fullResourceTypeList) {

            if (primaryPlace && !secondaryPlace) {
                this.resourceTypeList = this.fullResourceTypeList.filter(o => primaryPlace.resource_types.some(p => p.id == o.id));
            }
            else if (secondaryPlace) {
                this.resourceTypeList = this.fullResourceTypeList.filter(o => secondaryPlace.resource_types.some(p => p.id == o.id));
            }
            else {
                this.resourceTypeList = this.fullResourceTypeList;
            }

            if (this.selectedResourceType) {
                if (!this.resourceTypeList.some(o => o.id == this.selectedResourceType.id)) {
                    setTimeout(function () {
                        this.onSelectResourceType.emit(null);
                    }, 10);
                }
            }
        }
    }

    public OnlyNumbers($event) {
        let regex: RegExp = new RegExp(/^[0-9]{1,}$/g);
        let specialKeys: Array<string> = ['Backspace', 'Tab', 'End', 'Home', 'ArrowRight', 'ArrowLeft'];
        if (specialKeys.indexOf($event.key) !== -1) {
            return;
        } else {
            if (regex.test($event.key)) {
                return true;
            } else {
                return false;
            }
        }
    }

    public OnlyDecimal($event) {
        let regex: RegExp = new RegExp(/^[0-9\.]{1,}$/g);
        let specialKeys: Array<string> = ['Backspace', 'Tab', 'End', 'Home', 'ArrowRight', 'ArrowLeft'];
        if (specialKeys.indexOf($event.key) !== -1) {
            return;
        } else {
            if (regex.test($event.key)) {
                return true;
            } else {
                return false;
            }
        }
    }

    clearSelectedResourceType() {
        this.selectedResourceType = null;
        this.selectedResourceTypeLabel = null;
        this.changeResourceType(null);
        this.onSelectResourceType.emit(this.selectedResourceType);
    }

    caracChanged() {
        this.onSelectCaracList.emit(this.caracList);
    }
}



