import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import * as moment from 'moment';
import { AlertService, TenantConfigurationService } from '../../../modules/core';

@Component({
  selector: 'app-dates-selector',
  templateUrl: './dates-selector.component.html',
  styleUrls: ['./dates-selector.component.css']
})
export class DatesSelectorComponent implements OnInit {
  public mustShowDateStartWarning: boolean = false;
  public mustShowDateEndWarning: boolean = false;
  public mustShowDateMaxWarning: boolean = false;

  public start_date_string: string;
  public start_time_string: string;
  public end_date_string: string;
  public end_time_string: string;
  public delta: number;

  @Input() selectedStartDate: Date;
  @Input() selectedEndDate: Date;
  @Output() onSelectStartDate = new EventEmitter();
  @Output() onSelectEndDate = new EventEmitter();

  constructor(private tenantConfigurationService: TenantConfigurationService,
    private alertService: AlertService) { }

  ngOnInit() {

    var dateDebut: Date = new Date();
    this.start_date_string = this.roundedDateToDateString(dateDebut, moment.duration(15, "minutes"), "ceil");
    this.start_time_string = this.roundedDateToTimeString(dateDebut, moment.duration(15, "minutes"), "ceil");
    var dateFin: Date = new Date();
    this.delta = 60;
    dateFin.setMinutes(dateFin.getMinutes() + this.delta);
    this.end_date_string = this.roundedDateToDateString(dateFin, moment.duration(15, "minutes"), "ceil");
    this.end_time_string = this.roundedDateToTimeString(dateFin, moment.duration(15, "minutes"), "ceil");
    this.startDateSelected();
    this.endDateSelected();
  }

  roundedDateToDateString(date, duration, method): string {
    return moment(Math[method]((+date) / (+duration)) * (+duration)).format("YYYY-MM-DD");
  }
  roundedDateToTimeString(date, duration, method): string {
    return moment(Math[method]((+date) / (+duration)) * (+duration)).format("HH:mm");
  }

  startDateSelected() {
    //Eventuel arrondi
    var dateDebutTest: Date = new Date(this.start_date_string + " " + this.start_time_string);
    var previousQuarterOfHour = moment(new Date()).add(-15, 'minutes').toDate();
    if (dateDebutTest < previousQuarterOfHour) {
      this.mustShowDateStartWarning = true;
      setTimeout(() => {
        this.mustShowDateStartWarning = false;
      }, 3000);
      var dateDebut: Date = new Date();
      this.start_date_string = this.roundedDateToDateString(dateDebut, moment.duration(15, "minutes"), "ceil");
      this.start_time_string = this.roundedDateToTimeString(dateDebut, moment.duration(15, "minutes"), "ceil");
      this.startDateSelected();
    }
    else {
      if (this.isTooFar(dateDebutTest)) {
        this.mustShowDateMaxWarning = true;
        setTimeout(() => {
          this.mustShowDateMaxWarning = false;
        }, 3000);
        var dateDebut: Date = new Date();
        this.start_date_string = this.roundedDateToDateString(dateDebut, moment.duration(15, "minutes"), "ceil");
        this.start_time_string = this.roundedDateToTimeString(dateDebut, moment.duration(15, "minutes"), "ceil");
        this.startDateSelected();
      }
      else {
        this.start_date_string = this.roundedDateToDateString(dateDebutTest, moment.duration(15, "minutes"), "ceil");
        this.start_time_string = this.roundedDateToTimeString(dateDebutTest, moment.duration(15, "minutes"), "ceil");
        //Construction de la date INPUT    
        this.selectedStartDate = new Date(this.start_date_string + " " + this.start_time_string);
        this.onSelectStartDate.emit(this.selectedStartDate);

        // Maj date fin
        var dateFin: Date = new Date();
        dateFin.setTime(this.selectedStartDate.getTime() + this.delta * (1000 * 60));
        this.end_date_string = this.roundedDateToDateString(dateFin, moment.duration(15, "minutes"), "ceil");
        this.end_time_string = this.roundedDateToTimeString(dateFin, moment.duration(15, "minutes"), "ceil");
        this.endDateSelected();
      }
    }
  }
  endDateSelected() {
    //Eventuel arrondi
    var dateFinTest: Date = new Date(this.end_date_string + " " + this.end_time_string);
    if (dateFinTest <= this.selectedStartDate) {
      this.mustShowDateEndWarning = true;
      setTimeout(() => {
        this.mustShowDateEndWarning = false;
        //this.caracChanged();
      }, 3000);

      var dateFin: Date = new Date(this.start_date_string + " " + this.start_time_string);
      this.delta = 60;
      dateFin.setMinutes(dateFin.getMinutes() + this.delta);
      this.end_date_string = this.roundedDateToDateString(dateFin, moment.duration(15, "minutes"), "ceil");
      this.end_time_string = this.roundedDateToTimeString(dateFin, moment.duration(15, "minutes"), "ceil");
      this.endDateSelected();
    }
    else {
      if (this.isTooFar(dateFinTest)) {
        this.mustShowDateMaxWarning = true;
        setTimeout(() => {
          this.mustShowDateMaxWarning = false;
          //this.caracChanged();
        }, 3000);

        var dateFin: Date = new Date(this.start_date_string + " " + this.start_time_string);
        this.delta = 60;
        dateFin.setMinutes(dateFin.getMinutes() + this.delta);
        this.end_date_string = this.roundedDateToDateString(dateFin, moment.duration(15, "minutes"), "ceil");
        this.end_time_string = this.roundedDateToTimeString(dateFin, moment.duration(15, "minutes"), "ceil");
        this.endDateSelected();
      }
      else {
        this.end_date_string = this.roundedDateToDateString(dateFinTest, moment.duration(15, "minutes"), "ceil");
        this.end_time_string = this.roundedDateToTimeString(dateFinTest, moment.duration(15, "minutes"), "ceil");
        //Construction de la date INPUT    
        this.selectedEndDate = new Date(this.end_date_string + " " + this.end_time_string);
        this.onSelectEndDate.emit(this.selectedEndDate);
        //Recalcul du delta
        this.delta = (this.selectedEndDate.getTime() - this.selectedStartDate.getTime()) / (1000 * 60);
      }
    }
  }
  testDateValid(event) {
    // Prevent DEL and BACKSPACE
    if (event.keyCode === 8 || event.keyCode === 46) {
      event.stopPropagation();
      return false;
    }
  }
  isTooFar(dateTest: Date) {
    let max: number = this.tenantConfigurationService.MaxSynchro;
    let dateMax: Date = moment().add(max, 'd').toDate();
    return (dateTest > dateMax);
  }
}



