export class CalendarItem {

    public title: string;
    public description: string;
    public start: Date;
    public end: Date;

    constructor(obj?: any) {
        this.title = obj && obj.title || null;
        this.description = obj && obj.description || null;
        this.start = obj && obj.start || null;
        this.end = obj && obj.end || null;
    }

}


