import { ReservationService } from '../../../services/reservation/reservation.service';
import { ResourceService } from '../../../services/resource/resource.service';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { EventEmitter, Input, Output, SimpleChanges, OnChanges } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Observable, Subject, BehaviorSubject, Subscription } from 'rxjs';
import { Resource } from '../../../model/location/resource';
import { Reservation } from '../../../model/location/reservation';
import { Router } from '../../../../../node_modules/@angular/router';
import { ConfirmationDialogService } from 'app/view/confirmation-dialog/confirmation-dialog.service';

@Component({
  selector: 'app-my-reservations-master',
  templateUrl: './my-reservations-master.component.html',
  styleUrls: ['./my-reservations-master.component.css']
})
export class MyReservationsMasterComponent implements OnInit, OnDestroy {
  subscription: any;
  public loading = false;
  public mustShowModificationImpossibleWarning: boolean = false;

  //editField: string;
  reservationList: Array<any> = [];
  @Input() selectedReservation: Reservation;
  @Output() onSelectReservation: EventEmitter<any> = new EventEmitter();

  constructor(private translateService: TranslateService,
    private reservationService: ReservationService,
    private resourceService: ResourceService,
    private confirmationDialogService: ConfirmationDialogService,
    private router: Router) {
  }

  ngOnInit() {
    // var test = new Attendee
    this.loadData();
    let timer = Observable.timer(0, 10000);
    this.subscription = timer.subscribe(t => this.ticks());
  }

  // After the user live the page
  ngOnDestroy() {
    console.log('My reservations : timer destroyed');
    this.subscription.unsubscribe();
  }

  ticks() {
    this.loadData();
    this.buildButtonsVisibility();
  }
  public loadData(): void {
    var data: any;
    var data2: any;
    data = [0];
    this.loading = true;
    //Get Data
    this.getData()
      .then(r => {
        data = r;
        data.forEach(element => {
          this.getResource(element.id_resource)
            .then(r2 => {
              data2 = r2;
              element.resource = data2;
              element.resource.capacity = this.deduceCapacity(element.resource);
            }
            )
            .catch(err => {
              console.log("err => ressource non trouvée");
              this.loading = false;
            })
        });
        //Sort data 
        data.sort(this.compareFunction);

        this.reservationList = data;
        this.buildButtonsVisibility();
        this.loading = false;
      }
      )
      .catch(err => {
        console.log("err => réservation non trouvée");
        this.loading = false;
      })
  }
  public deduceCapacity(res: Resource): string {
    var retour: string = '';
    res.charac.forEach(car => {
      if (car.label === 'Capacité') {
        retour = car.value;
      }
    });
    if (retour === '') {
      retour = res.resource_type.label.charAt(0).toUpperCase();
    }
    return retour;
  }
  compareFunction(a, b) {
    if (a.start_date > b.start_date) {
      return 1;
    }
    if (a.start_date < b.start_date) {
      return -1;
    }
    return 0;
  }
  getData() {
    // keep this for Promise
    var that = this;


    return new Promise(function (resolve, reject) {
      that.reservationService.getReservations().subscribe(
        data => {
          resolve(data);
        },
        error => {
          reject();
        })
    });
  }
  getResource(id: number) {
    // keep this for Promise
    var that = this;


    return new Promise(function (resolve, reject) {
      that.resourceService.getResourceById(id).subscribe(
        data => {
          resolve(data);
        },
        error => {
          reject();
        })
    });
  }

  doAction(event, reservation) {
    if (reservation.startButtonVisible) {
      //alert("START !");
      this.startReservation(reservation);
    }
    else {

      if (reservation.closeButtonVisible) {
        //alert("CLOSE !");
        this.confirmationDialogService.confirm(
          this.translateService.instant('CONFIRMATION-TITLE'),
          this.translateService.instant('CONFIRMATION-CLOSE-CAPTION'),
          this.translateService.instant('BUTTON-YES'),
          this.translateService.instant('BUTTON-NO'))
          .then((confirmed) => {
            if (confirmed) {
              this.deleteReservation(reservation)
            }
          })
          // .catch(() => console.log('Suppression annulée'));
          .catch(function (error) {
            console.log(error);
          });
      }
      else {
        if (reservation.stopButtonVisible) {
          //alert("STOP !");
          this.confirmationDialogService.confirm(
            this.translateService.instant('CONFIRMATION-TITLE'),
            this.translateService.instant('CONFIRMATION-STOP-CAPTION'),
            this.translateService.instant('BUTTON-YES'),
            this.translateService.instant('BUTTON-NO'))
            .then((confirmed) => {
              if (confirmed) {
                this.deleteReservation(reservation)
              }
            })
            // .catch(() => console.log('Suppression annulée'));
            .catch(function (error) {
              console.log(error);
            });

        }
        else {
          if (reservation.deleteButtonVisible) {
            this.confirmationDialogService.confirm(
              this.translateService.instant('CONFIRMATION-TITLE'),
              this.translateService.instant('CONFIRMATION-DELETE-CAPTION'),
              this.translateService.instant('BUTTON-YES'),
              this.translateService.instant('BUTTON-NO'))
              .then((confirmed) => {
                if (confirmed) {
                  this.deleteReservation(reservation)
                }
              })
              // .catch(() => console.log('Suppression annulée'));
              .catch(function (error) {
                console.log(error);
              });
          }

        }

      }

    }

    event.stopPropagation()
  }
  startReservation(res: Reservation) {
    this.reservationService.start(res.id)
      .subscribe(
        result => {
          if (result.status === 200) {
            console.log(result);
            // Let's disable the Reservation button
            res.stopButtonVisible = false;
            res.closeButtonVisible = false;
            res.deleteButtonVisible = false;

            //Reload
            setTimeout(() => {
              // We wait a little bit
              this.loadData();
            }, 100);
          }
          else {
            this.mustShowModificationImpossibleWarning = true;
            setTimeout(() => {
              this.mustShowModificationImpossibleWarning = false;
              //this.caracChanged();
            }, 3000);

          }
        },
        error => {
          console.log(error);
          //this.errors = error;
          this.mustShowModificationImpossibleWarning = true;
          setTimeout(() => {
            this.mustShowModificationImpossibleWarning = false;
            //this.caracChanged();
          }, 3000);
        }
      );

  }
  deleteReservation(res: Reservation) {
    this.reservationService.delete(res.id)
      .subscribe(
        result => {
          if (result.status === 200) {
            console.log(result);
            // Let's disable the Reservation button
            res.startButtonVisible = false;

            //Reload
            setTimeout(() => {
              // We wait a little bit
              this.loadData();
            }, 100);
          }
          else {
            this.mustShowModificationImpossibleWarning = true;
            setTimeout(() => {
              this.mustShowModificationImpossibleWarning = false;
            }, 3000);
          }
        },
        error => {
          console.log(error);
          //this.errors = error;
          this.mustShowModificationImpossibleWarning = true;
          setTimeout(() => {
            this.mustShowModificationImpossibleWarning = false;
            //this.caracChanged();
          }, 3000);
        }
      );

  }
  reservationSelected(res: Reservation) {
    this.selectedReservation = res;
    this.onSelectReservation.emit(this.selectedReservation);
  }

  buildButtonsVisibility() {
    if (this.reservationList) {
      this.reservationList.forEach(res => {
        res.startButtonVisible = this.buildStartButtonVisibility(res);
        res.closeButtonVisible = this.buildCloseButtonVisibility(res);
        res.stopButtonVisible = this.buildStopButtonVisibility(res);
        res.deleteButtonVisible = this.buildDeleteButtonVisibility(res);
      });
    }
  }

  buildStartButtonVisibility(res: Reservation): boolean {
    let millisecondesAfterStart = new Date().getTime() - res.start_date;
    let millisecondesBeforeEnd = res.end_date - new Date().getTime();
    if (res.state === 'Confirmed') {
      if (millisecondesAfterStart > 0 && millisecondesBeforeEnd > 0) {
        return true;
      }
      else {
        return false;
      }

    }
    else {
      return false;
    }

  }
  buildCloseButtonVisibility(res: Reservation): boolean {
    if (res.state === 'Created' || res.state === 'Confirmed' || res.state === 'Updating') {
      return true;
    }
    else {
      return false;
    }

  }
  buildStopButtonVisibility(res: Reservation): boolean {
    if (res.state === 'Started' || res.state === 'Extended') {
      if (!this.buildStartButtonVisibility(res)) {
        return true;
      }
      else {
        return false;
      }
    }
    else {
      return false;
    }
  }

  buildDeleteButtonVisibility(res: Reservation): boolean {
    if (res.state === 'Refused') {
      return true;
    }
    else {
      return false;
    }
  }
}



