import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Observable } from '../../../../../node_modules/rxjs';
import { TreeService } from '../../../services/tree/tree.service';
import { TranslateService } from '../../../../../node_modules/@ngx-translate/core';
import { ResourceService } from '../../../services/resource/resource.service';
import { FilterSearchPipe } from '../../../services/tools/pipe/filter-search.pipe';


@Component({
  selector: 'app-search-text-selector',
  templateUrl: './search-text-selector.component.html',
  styleUrls: ['./search-text-selector.component.css']
})
export class SearchTextSelectorComponent implements OnInit {
  @Input() selectedSearchText: string;
  @Input() where: string;    
  @Output() onSelectSearchText = new EventEmitter();
  @Output() onEnterKeyPressed: EventEmitter<any> = new EventEmitter();

  constructor(private treeService: TreeService, private resourceService: ResourceService, private translateService: TranslateService) { }

  ngOnInit() {
    
  }
  searchTextSelected() {
    this.onSelectSearchText.emit(this.selectedSearchText);
  }

  clearSearchText() {
    this.selectedSearchText = '';
    this.searchTextSelected();
    //It takes time for SearchPageRessources to be aware that selectedSearchText has changed
    setTimeout(() => {
      this.onEnterKeyPressed.emit(); 
    }, 100);
  }

  triggerLaunchSearch() {
    this.onEnterKeyPressed.emit();
  }
}



