import { NgbModule } from '@ng-bootstrap/ng-bootstrap'
import { CarouselModule } from 'ngx-bootstrap/carousel';
import { AngularFontAwesomeModule } from 'angular-font-awesome/angular-font-awesome';
import { MultiselectDropdownModule } from 'angular-2-dropdown-multiselect';
import { ChartsModule } from 'ng2-charts/ng2-charts';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { SchedulerModule } from '@progress/kendo-angular-scheduler';
import { LOCALE_ID, NgModule, APP_INITIALIZER } from '@angular/core';
import { Http, HttpModule, RequestOptions, XHRBackend } from '@angular/http';
import { RouterModule, Router, Routes, ExtraOptions } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TreeService } from './services/tree/tree.service';
import { ReportService } from './services/report/report.service';
import { BIService } from './services/bi/bi.service';
import { BIReportService } from './services/bi/bi-report.service';
import { BIT1ReportsService } from './services/bi/bi-t1-report.service';
import { ReservationService } from './services/reservation/reservation.service';
import { ResourceService } from './services/resource/resource.service';
import { MapwizeService } from './services/mapwize/mapwize.service';
import { AppComponent } from './app.component';
import { NavbarComponent } from './view/navbar/navbar.component';
import { ImgSecureComponent } from './view/partial/img-secure/img-secure.component';
import { ReportLocationSearchPipe } from './services/tools/pipe/report-location-search.pipe';
import { ReservationStatePipe } from './services/tools/pipe/reservation-state-pipe';
import { AuthenticationModule } from 'app/modules/authentication';
import { CoreModule, HttpService, AppConfigurationService, TenantConfigurationService } from 'app/modules/core';
import { AppRoutingModule } from 'app/app-routing.module';
import { TranslateModule } from 'app/modules/translate/translate.module';
import { TranslateModule as NgxTranslateModule, TranslateLoader } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { DatexPipe } from './services/tools/pipe/datex-pipe';
import { LoadingModule, ANIMATION_TYPES } from 'ngx-loading';
import { environment } from 'environments/environment';
import { TreeviewModule } from 'ngx-treeview';
// import { TreeModule } from 'angular-tree-component';
import { TreeModule } from 'ng2-tree';
import { HomeComponent } from './view/home/home.component';
import { MyReservationsWrapperComponent } from './view/my-reservations/my-reservations-wrapper/my-reservations-wrapper.component';
import { MyReservationsMasterComponent } from './view/my-reservations/my-reservations-master/my-reservations-master.component';
import { MyReservationsDetailComponent } from './view/my-reservations/my-reservations-detail/my-reservations-detail.component';
import { SearchPageWrapperComponent } from './view/search-page/search-page-wrapper/search-page-wrapper.component';
import { SearchPageResourcesComponent } from './view/search-page/search-page-resources/search-page-resources.component';
import { SearchPageDetailComponent } from './view/search-page/search-page-detail/search-page-detail.component';
import { DatesSelectorComponent } from './view/search-page/dates-selector/dates-selector.component';
import { Globals } from './globals';
import { ResourceTypeSelectorComponent } from './view/search-page/resource-type-selector/resource-type-selector.component';
import { PlaceSelectorComponent } from './view/search-page/place-selector/place-selector.component';
import { SearchTextSelectorComponent } from './view/search-page/search-text-selector/search-text-selector.component';
import { MySettingsWrapperComponent } from './view/my-settings/my-settings-wrapper/my-settings-wrapper.component';
import { MySettingsFavouritePlacesComponent } from './view/my-settings/my-settings-favourite-places/my-settings-favourite-places.component';
import { ConfirmationDialogComponent } from './view/confirmation-dialog/confirmation-dialog.component';
import { ConfirmationDialogService } from './view/confirmation-dialog/confirmation-dialog.service';
import { ShowUrlComponent } from './view/search-page/show-url/show-url.component';
import { ShowCalendarComponent } from './view/search-page/show-calendar/show-calendar.component';
import { ShowUrlService } from './view/search-page/show-url/show-url.service';
import { ShowCalendarService } from './view/search-page/show-calendar/show-calendar.service';
import {ShowMapwizeService} from './view/search-page/show-mapwize/show-mapwize.service';
import '@progress/kendo-angular-intl/locales/fr/all';
import { MapwizeComponent } from './view/mapwize/mapwize.component';
import { ShowMapwizeComponent } from './view/search-page/show-mapwize/show-mapwize.component';


import { AvailabilitiesWrapperComponent } from './view/availabilities/availabilities-wrapper/availabilities-wrapper.component';
import { FilterSearchPipe } from './services/tools/pipe/filter-search.pipe';

export function loadConfiguration(configService: AppConfigurationService): () => void {
    return () => configService.load();
}
// export function loadTenantConfiguration(tenantConfigService: TenantConfigurationService): () => void {
//     return () => tenantConfigService.load();
// }
export function createTranslateLoader(configService: AppConfigurationService, http: Http) {
    return new TranslateHttpLoader(http, configService.urlV2 + 'assets/i18n/', '.json');
}


@NgModule({
    declarations: [
        AppComponent,
        NavbarComponent,
        ImgSecureComponent,
        ReportLocationSearchPipe,
        ReservationStatePipe,
        DatexPipe,
        HomeComponent,
        SearchPageWrapperComponent,
        SearchPageResourcesComponent,
        SearchPageDetailComponent,
        MyReservationsWrapperComponent,
        MyReservationsMasterComponent,
        MyReservationsDetailComponent,
        DatesSelectorComponent,
        ResourceTypeSelectorComponent,
        PlaceSelectorComponent,
        SearchTextSelectorComponent,
        MySettingsWrapperComponent,
        MySettingsFavouritePlacesComponent,
        ConfirmationDialogComponent,
        ShowUrlComponent,
        ShowCalendarComponent,
        AvailabilitiesWrapperComponent,
        MapwizeComponent,
        ShowMapwizeComponent,
        FilterSearchPipe
    ],
    imports: [
        NgbModule.forRoot(),
        CarouselModule.forRoot(),
        ChartsModule,
        AngularFontAwesomeModule,
        MultiselectDropdownModule,
        BrowserModule,
        BrowserAnimationsModule,
        SchedulerModule,
        FormsModule,
        ReactiveFormsModule,
        HttpModule,
        AuthenticationModule,
        CoreModule,
        AppRoutingModule,
        LoadingModule.forRoot({
            animationType: ANIMATION_TYPES.threeBounce,
            backdropBackgroundColour: 'rgba(255,255,255,1)',
            backdropBorderRadius: '2px',
            primaryColour: '#00aaff',
            secondaryColour: '#00aaff',
            tertiaryColour: '#00aaff'
        }),
        NgxTranslateModule.forRoot({
            loader: {
                provide: TranslateLoader,
                useFactory: createTranslateLoader,
                deps: [AppConfigurationService, Http],
            },
        }),
        TreeviewModule.forRoot(),
        TreeModule,
    ],
    providers: [
        {
            provide: APP_INITIALIZER,
            useFactory: loadConfiguration,
            deps: [AppConfigurationService],
            multi: true,
        },
        { provide: LOCALE_ID, useValue: 'fr' },
        // {
        //     provide: APP_INITIALIZER,
        //     useFactory: loadTenantConfiguration,
        //     deps: [TenantConfigurationService],
        //     multi: true,
        // },
        ReportService,
        TreeService,
        BIService,
        BIReportService,
        BIT1ReportsService,
        ReservationService,
        ResourceService,
        MapwizeService,
        HttpService,
        AppConfigurationService,
        TenantConfigurationService,
        ConfirmationDialogService,
        ShowUrlService,
        ShowCalendarService,
        SearchPageResourcesComponent,
        SearchPageDetailComponent,
        PlaceSelectorComponent,
        ResourceTypeSelectorComponent,
        Globals,
        ShowMapwizeService
    ],
    bootstrap: [AppComponent],
    entryComponents: [ ConfirmationDialogComponent, ShowUrlComponent, ShowCalendarComponent, ShowMapwizeComponent ],

})
export class AppModule { }



