import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { environment } from '../../../../environments/environment';

@Injectable()
/**
 * this service manages global configuration of the application
 */
export class AppConfigurationService {
    private configuration: any;

    constructor(private http: Http) { }

    public async load(): Promise<AppConfigurationService> {
        // console.log('loading configuration: ' + environment.configurationFile);
        const result = await this.http.get('assets/' + environment.configurationFile).toPromise();
        // console.log('configuration');
        this.configuration = result.json();
        // console.log(this.configuration);

        return this;
    }

    public get clientID(): string {
        return this.configuration.clientID;
    }
    public get urlAccueil(): string {
        return this.configuration.urlAccueil;
    }
    public get urlAPIV1(): string {
        return this.configuration.urlAPIV1;
    }
    public get urlV2(): string {
        return this.configuration.urlV2;
    }
    public get env(): string {
        return this.configuration.env;
    }
    public get pictoAddress(): string {
        return this.configuration.pictoAddress;
    }
    public get mapwize(): string {
        return this.configuration.mapwize;
    }

}



