import { ResourceType } from "app/model/location/sub/resource-type";

export abstract class Location {

    public id: number;
    public label: string;
    public id_place_parent: number;
    public resource_types: ResourceType[];

    constructor(obj?: any) {
        this.id = obj && obj.id || null;
        this.label = obj && obj.label || null;
        this.id_place_parent = obj && obj.id_place_parent || null;
        this.resource_types = obj && obj.resource_types || null;
    }
}


