import { Location } from './location';
import { Address } from './sub/address';
import { Charac } from './sub/charac';
import { Complement } from './sub/complement';
import { ResourceType } from './sub/resource-type';
import { Picture } from './sub/picture';

export class Resource extends Location {

    public resource_type: ResourceType;
    public address: Address;
    public charac: Charac[] = [];
    public complement_type: Complement[] = [];
    public capacity: number;
    public  map_url: string;
    public picture: Picture[] = [];

    constructor(obj?: any) {
        super(obj);

//        this.id_resource_type = obj && obj.id_resource_type || null;
        // this.id_resource_type = obj && obj.resource_type && obj.resource_type.id || null;
        
        if (obj && obj.address) {
            this.address = new Address(obj.address)
        }
        if (obj && obj.resource_type) {
            this.resource_type = new ResourceType(obj.resource_type)
        }
        if (obj && obj.charac) {
            obj.charac.forEach( c => {
                let tmp = new Charac(c);
                this.charac.push(c);
            })
        }
        if (obj && obj.complement_type) {
            obj.complement_type.forEach( c => {
                let tmp = new Complement(c);
                this.complement_type.push(c);
            })
        }
        this.capacity = obj && obj.capacity || null;
        this.map_url = obj && obj.map_url || null;
        if (obj && obj.picture) {
            obj.picture.forEach( c => {
                let tmp = new Picture(c);
                this.picture.push(c);
            })
        }

    }

}


