import { Location } from './location';

export class PlaceWithChildren extends Location {

    public id_place_type: number;
    public child_places: PlaceWithChildren[];

    constructor(obj?: any) {
        super(obj);
        
        this.id_place_type = obj && obj.id_place_type || null;
        this.child_places = obj && obj.child_places || null;
    }
}


