
export class CharacComplementType {

    public id: number;
    public value: string;
    public type: string;
    public reservation: number;
    public tenant_id: number;
    public id_charactype_complement_type: number;

    constructor(obj?: any) {
        this.id = obj && obj.id || null;
        this.value = obj && obj.value || null;
        this.type = obj && obj.type || null;
        this.reservation = obj && obj.reservation || null;
        this.tenant_id = obj && obj.tenant_id || null;
        this.id_charactype_complement_type = obj && obj.id_charactype_complement_type || null;

    }

}


