import { Component, OnInit, ViewChild } from '@angular/core';
import { MapwizeService } from '../../services/mapwize/mapwize.service';
import { PlaceWithChildren } from '../../model/location/place-with-children';
import { PlaceSelectorComponent } from 'app/view/search-page/place-selector/place-selector.component';
import { AppConfigurationService, TenantConfigurationService } from 'app/modules/core';

@Component({
  selector: 'app-mapwize',
  templateUrl: './mapwize.component.html',
  styleUrls: ['./mapwize.component.css']
})
export class MapwizeComponent implements OnInit {
  
  selectedPrimary: PlaceWithChildren = null;
  selectedSecondary: PlaceWithChildren = null;
  selectedPrimaryHasChanged = false;
  selectedSecondaryHasChanged = false;
  
  @ViewChild(PlaceSelectorComponent) placeSelectorComponent: PlaceSelectorComponent;

  constructor(
    private mapwizeService:MapwizeService,
    private tenantConfService:TenantConfigurationService,
    private appConfigurationService:AppConfigurationService
  ) {}
  
  ngOnInit() {
    this.mapwizeService.container="mapwize";  
  }

  onSelectPrimary(primaryPlace: PlaceWithChildren) {
    // console.log(primaryPlace);    
    this.selectedPrimary = primaryPlace;
    this.selectedPrimaryHasChanged = true;
  }

  onSelectSecondary(secondaryPlace: PlaceWithChildren) {
    // console.log(secondaryPlace);    
    this.selectedSecondary = secondaryPlace;
    this.selectedSecondaryHasChanged = true;

    this.mapwizeService.clearMap();
    this.initMap();
  }

  initMap(){
    this.mapwizeService.searchVenueByName(this.selectedSecondary).then((venueId)=>{
      this.mapwizeService.mapCreation()
        .then(mapInstance => {    
          mapInstance.centerOnVenue(venueId);    
          mapInstance.on('mapwize:venueenter', (e) => {    
            mapInstance.setUniverse(this.mapwizeService.universeId);
          });      
        });
    });
  }

}



