import { Component, OnInit, OnDestroy } from '@angular/core';
import { AuthService } from 'app/modules/authentication';
import { AppConfigurationService, TenantConfigurationService } from 'app/modules/core';
import { Observable } from '../../../../node_modules/rxjs';
import { Router } from '@angular/router';
import { Globals } from 'app/globals';


@Component({
    selector: 'app-navbar',
    templateUrl: './navbar.component.html',
    styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit, OnDestroy {
    subscription: any;
    records = [
        {
            "label": "GENERAL",
            "Icon": "fa-home",
            "content": [
                {
                    "KeyName": "HOME",
                    "PageUrl": "home",

                }
                ,
                {
                    "KeyName": "MY-SETTINGS-TITLE",
                    "PageUrl": "my-settings",

                }
            ]
        },
        {
            "label": "RESOURCES",
            "Icon": "fa-calendar-check-o",
            "content": [
                {
                    "KeyName": "RESERVATION-TITLE",
                    "PageUrl": "search-page",
                }
                ,
                {
                    "KeyName": "AVAILABILITY-TITLE",
                    "PageUrl": "availabilities",
                }
                ,
                {
                    "KeyName": "MY-RESERVATIONS-TITLE",
                    "PageUrl": "my-reservations",
                },
                {
                    "KeyName": "Mapwize",
                    "PageUrl": "mapwize",
                }
            ]
        }
    ]

    public logoTenant;
    public labelTenant;

    constructor(private authService: AuthService,
        private appConfigurationService: AppConfigurationService,
        private router: Router,
        private globals: Globals,
        private tenantConfigurationService: TenantConfigurationService) {
        if (tenantConfigurationService.logoImageData) {
            this.logoTenant = 'data:image/png;base64,' + tenantConfigurationService.logoImageData;
            this.labelTenant = tenantConfigurationService.label;
        }
    }

    async ngOnInit() {
        if (!this.tenantConfigurationService.logoImageData) {
            // logoImageData not available yet : let's try every 100 ms until it's here
            let timer = Observable.timer(0, 100);
            this.subscription = timer.subscribe(t => this.ticks());
        }
        else {
            // console.log('Navbar => logo found without delay');
            this.loadImage();
        }
    }

    ticks() {
        // console.log('Navbar => logo not found => checking again...');
        if (this.logoTenant === undefined) {
            this.loadImage();
        }
        else {
            // logo has finally arrived... timer is not needed anymore
            // console.log('Navbar => logo finally found!');
            this.subscription.unsubscribe();
        }
    }
    loadImage() {
        if (this.tenantConfigurationService.logoImageData) {
            this.logoTenant = 'data:image/png;base64,' + this.tenantConfigurationService.logoImageData;
            this.labelTenant = this.tenantConfigurationService.label;
        }
    }
    public logout(): void {
        this.authService.logout();
    }
    // After the user leaves the page
    ngOnDestroy() {
        // console.log('Navbar : timer destroyed');
        if (this.subscription)
            this.subscription.unsubscribe();
        // else
            // console.log('Navbar : timer already destroyed');
    }

    calculateClasses(label: string) {
        let found:boolean = false;
        let record = this.records.find(r => r.label === label);
        if (record) { 
            record.content.forEach(element => {
             if (this.router.url === '/' + element.PageUrl)
             {
                 found= true;
             }   
            });
        }
        if (found) {
            return {
                active: true
            };
        }
        else {
            return {
                active: false
            };
        }
        // switch (label) {
        //     case 'GENERAL':
        //         if (this.router.url === '/home' || this.router.url === '/my-settings') {
        //             return {
        //                 active: true
        //             };
        //         }
        //         else {
        //             return {
        //                 active: false
        //             };
        //         }
        //     case 'RESOURCES':
        //         if (this.router.url === '/search-page' || this.router.url === '/my-reservations') {
        //             return {
        //                 active: true
        //             };
        //         }
        //         else {
        //             return {
        //                 active: false
        //             };
        //         }


        //     default:
        //         return {
        //             active: false
        //         };
        // }
    }

}



