import { Injectable } from '@angular/core';

import { Http, Headers, Request, Response } from '@angular/http';
import { Observable, BehaviorSubject } from 'rxjs/Rx';
import { UserLocal, AppConfigurationService } from 'app/modules/core';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { Office365Service } from 'app/modules/authentication/modules/office365';
import { ZenOfficeAuth } from 'app/modules/authentication/modules/zenoffice/services/zenoffice-auth.service';
import { Router } from '@angular/router';
import { InfoLocalStorageService } from 'app/modules/localStorage';


@Injectable()
export class AuthService {

    public temporaryPermission: string = '';
    public _isOnLoginPageAuthenticated$: BehaviorSubject<boolean>;
    private _waitingForAuthentication$: BehaviorSubject<boolean>

    constructor(
        private office365Service: Office365Service,
        private zenOfficeAuth: ZenOfficeAuth,
        private router: Router,
        private infoLocalStorageService: InfoLocalStorageService,
        private appConfigurationService: AppConfigurationService) {
        this._isOnLoginPageAuthenticated$ = new BehaviorSubject<boolean>(false);
        const waiting = this.infoLocalStorageService.isWaitingForAuthToComplete;
        if (waiting) {
            setTimeout(() => {
                this.infoLocalStorageService.isWaitingForAuthToComplete = 'false';
                this._waitingForAuthentication$.next(false);
            }, 5000);
        }
        this._waitingForAuthentication$ = new BehaviorSubject<boolean>(waiting);
    }

    public async login(): Promise<void> {
        this.infoLocalStorageService.isWaitingForAuthToComplete = 'true';
        this._waitingForAuthentication$.next(true);
        await this.office365Service.login();
        // console.log('offic365 Auth succeed');
    }

    public async loginZenOffice(): Promise<void> {
        this._waitingForAuthentication$.next(false);
        await this.zenOfficeAuth.loginOAuth();
        // console.log('zenOffice Auth succeed');
    }

    public async isAuthenticated(): Promise<boolean> {
        
        const isOffice365Auth = await this.isOffice365Authenticated();
        const isZenOfficeAuth =  await this.isZenOfficeAuthenticated();

        if (!isOffice365Auth && isZenOfficeAuth) {
            this.logout();
        } else if (isOffice365Auth && isZenOfficeAuth) {
            return true;
        }

        return false;
    }

    public async isZenOfficeAuthenticated() :  Promise<boolean>{

        const isZenOfficeAuth = await this.zenOfficeAuth.isAuthenticated();
        // console.log('Zen Office is authenticated with ZenOffice', isZenOfficeAuth);
        
        return isZenOfficeAuth;
    }

    public async isOffice365Authenticated() :  Promise<boolean>{

        const isOffice365Auth = await this.office365Service.isAuthenticated();
        // console.log('Zen Office is authenticated with Office365', isOffice365Auth);

        return isOffice365Auth;
    }

    public logout(): void {
        localStorage.clear();
        window.location.href = this.appConfigurationService.urlV2 + 'login';
        //this.router.navigate(['login']);
    }

    public getPermission(feature, value) {
        // todo : implementation
        return true;
    }

    public getRoutePermission(route): boolean {
        // todo : implementation
        return true;
    }

    public waitingForAuthenticationSubscriber(): Observable<boolean> {
        return this._waitingForAuthentication$;
    }
}



