import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { AuthGuard } from 'app/modules/authentication/services/auth.guard';
import { LoginComponent } from 'app/modules/authentication/views/login.component';
// import { BiViewerComponent } from 'app/view/bi-viewer/bi-viewer.component';
import { SearchPageWrapperComponent } from 'app/view/search-page/search-page-wrapper/search-page-wrapper.component';
import { HomeComponent } from 'app/view/home/home.component';
import { MyReservationsWrapperComponent } from 'app/view/my-reservations/my-reservations-wrapper/my-reservations-wrapper.component';
import { MySettingsWrapperComponent } from './view/my-settings/my-settings-wrapper/my-settings-wrapper.component';
import { AvailabilitiesWrapperComponent } from './view/availabilities/availabilities-wrapper/availabilities-wrapper.component';

// setup allowed routes and associated components
import { MapwizeComponent } from 'app/view/mapwize/mapwize.component';
import { ShowMapwizeComponent } from 'app/view/search-page/show-mapwize/show-mapwize.component';

const routes: Routes = [
    {
        path: './',
        redirectTo: 'home',
        canActivate: [AuthGuard]
    },
    {
        path: 'home',
        component: HomeComponent,
        canActivate: [AuthGuard]
    },
    {
        path: 'search-page',
        component: SearchPageWrapperComponent,
        canActivate: [AuthGuard]
    },
    {
        path: 'my-reservations',
        component: MyReservationsWrapperComponent,
        canActivate: [AuthGuard]
    },  
    {
        path: 'availabilities',
        component: AvailabilitiesWrapperComponent,
        canActivate: [AuthGuard]
    },  
    {
        path: 'my-settings',
        component: MySettingsWrapperComponent,
        canActivate: [AuthGuard]
    },
    {
        path: 'mapwize',
        component: MapwizeComponent,
        canActivate: [AuthGuard]
    },
    {
        path: 'show-mapwize',
        component: ShowMapwizeComponent,
        canActivate: [AuthGuard]
    },
    {
        path: '**',
        redirectTo: 'home'
    }
];

@NgModule({
    imports: [
        RouterModule.forRoot(routes),
    ],
    exports: [
        RouterModule,
    ],
})
export class AppRoutingModule { }



