import { Injectable } from '@angular/core';

@Injectable()
export class Office365LocalStorageService {
    private _userOffice365: any;
    private _tokenOffice365: any;

    public get userOffice365(): any {
        const user = localStorage.getItem('office365_user');
        return this.getObject(user);
    }

    public set userOffice365(user: any) {
        localStorage.setItem('office365_user', JSON.stringify(user));
    }

    public get tokenOffice365(): any {
        return localStorage.getItem('office365_token');
    }

    public set tokenOffice365(token: any) {
        localStorage.setItem('office365_token', token);
    }

    private getObject(object: any): any {
        if (object !== 'undefined') {
            return JSON.parse(object);
        } else {
            return null;
        }
    }
}


