export class Incident {

    public id: number;
    public label: string;
    public parent_id: number;
    public children: Incident[]=[];

    //Champs calculés
    public family_label: string;

    constructor(obj?: any) {
        this.id = obj && obj.id || null;
        this.label = obj && obj.label || null;
        this.parent_id = obj && obj.parent_id || null;
        if (obj && obj.children) {
            obj.children.forEach( c => {
                let tmp = new Incident(c);
                this.children.push(tmp);
            })
        }

    }

}



