import { Injectable } from '@angular/core';
import { Http, Headers, Request, Response } from '@angular/http';
import { Observable, Subject, BehaviorSubject, Subscription } from 'rxjs';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { environment } from 'environments/environment';
import { Serializable } from '../../model/utils';
import { Place } from '../../model/location/place';
import { PlaceWithChildren } from '../../model/location/place-with-children';
import { Resource } from '../../model/location/resource';
import { ResourceType } from '../../model/location/sub/resource-type';
import { SearchResource } from '../../model/location/searchResource';
import { LocationType } from '../../model/location/location-type';
import { HttpService, PathService } from 'app/modules/core';

@Injectable()
export class TreeService {

    constructor(private http: HttpService,
        private pathService: PathService) { }

    getPlaceById(id: number) {

        return this.http.get(this.pathService.getApiPath('PLACE') + '/' + id)
            .map((res: Response) => {
                let data = res.json();

                if (data) {
                    let tmp = new Serializable(data);
                    data = new Place(tmp);
                }

                return data;
            });
    }

    getResourceById(id: number) {

        return this.http.get(this.pathService.getApiPath('RESOURCE') + '/' + id)
            .map((res: Response) => {
                let data = res.json();

                if (data) {
                    let tmp = new Serializable(data);
                    data = new Resource(tmp);
                }

                return data;
            });
    }

    getPlaceTypeList(): Observable<LocationType[]> {
        let data: LocationType[] = [];
        return this.http.get(this.pathService.getApiPath('PLACE-TYPE'))
            .map((res: Response) => {

                let list = res.json();

                list.forEach(element => {
                    let tmp = new Serializable(element)
                    let tmp2 = new LocationType(tmp)
                    data.push(tmp2);
                });

                return data;

            });
    }


    addPlaceType(dataToSend) {

        return this.http.post(this.pathService.getApiPath('PLACE-TYPE'), dataToSend)
            .map((res: Response) => {
                return res.json();
            });

    }

    updatePlaceType(data) {

        let dataToSend = JSON.stringify(data);

        return this.http.put(this.pathService.getApiPath('PLACE-TYPE'), dataToSend)
            .map((res: Response) => {
                return res;
            });
    }

    getResourceTypeList() {
        let data: ResourceType[] = [];
        return this.http.get(this.pathService.getApiPath('RESOURCE-TYPE'))
            .map((res: Response) => {

                let list = res.json();

                list.forEach(element => {
                    let tmp = new Serializable(element)
                    let tmp2 = new ResourceType(tmp)
                    data.push(tmp2);
                });

                return data;

            });
    }
    getAllPlaces() {

        let data: PlaceWithChildren[] = [];
        return this.http.get(this.pathService.getApiPath('ALL-PLACES'))
            .map((res: Response) => {

                let list = res.json();

                list.forEach(element => {
                    let tmp = new Serializable(element)
                    let tmp2 = new PlaceWithChildren(tmp)
                    data.push(tmp2);
                });

                var sortedData: PlaceWithChildren[] =
                    data.sort((n1, n2) => {
                        if (n1.label > n2.label) {
                            return 1;
                        }

                        if (n1.label < n2.label) {
                            return -1;
                        }

                        return 0;
                    });
                return sortedData;

            });
    }
    getAllResources() {

        let data: SearchResource[] = [];
        return this.http.get(this.pathService.getApiPath('RESOURCE') + "?search=")
            .map((res: Response) => {

                let list = res.json();

                list.forEach(element => {
                    let tmp = new Serializable(element)
                    let tmp2 = new SearchResource(tmp)
                    data.push(tmp2);
                });
                var sortedData: SearchResource[] =
                    data.sort((n1, n2) => {
                        if (n1.resource.label > n2.resource.label) {
                            return 1;
                        }

                        if (n1.resource.label < n2.resource.label) {
                            return -1;
                        }

                        return 0;
                    });
                return sortedData;

            });
    }

    UpdateFavouritePlaces(obj: Place[]) {

        let tmp: any = obj;

        let dataToSend = JSON.stringify(tmp);

        return this.http.post(this.pathService.getApiPath('FAVOURITE-PLACES'), dataToSend)
            .map((res: Response) => {
                return res.json();
            });

    }

    getFavouritePlaces() {

        let data: Place[] = [];
        return this.http.get(this.pathService.getApiPath('FAVOURITE-PLACES'))
            .map((res: Response) => {

                let list = res.json();

                list.forEach(element => {
                    let tmp = new Serializable(element)
                    let tmp2 = new Place(tmp)
                    data.push(tmp2);
                });

                return data;
            });
    }
}



