import { Injectable } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ShowUrlComponent } from './show-url.component';

@Injectable()
export class ShowUrlService {

  constructor(private modalService: NgbModal) { }

  public show(
    url:string,
    title: string,
    btnCancelText: string = 'Annuler',
    dialogSize: 'sm'|'lg' = 'lg'): Promise<boolean> {
    const modalRef = this.modalService.open(ShowUrlComponent, { windowClass: 'modal-dialog-centered', size: dialogSize });
    modalRef.componentInstance.urlParam = url;
    modalRef.componentInstance.title = title;
    modalRef.componentInstance.btnCancelText = btnCancelText;

    return modalRef.result;
  }

}



