import { Injectable } from '@angular/core';

@Injectable()
export class InfoLocalStorageService {

    private _user: any;
    private _token: any;
    private _rememberMeToken: any;
    private _isWaitingForAuthToComplete: any

    public set token(token: any) {
        localStorage.setItem('token', token);
    }

    public get token() {
        return localStorage.getItem('token');
    }

    public set rememberMeToken(rememberMeToken: any) {
        localStorage.setItem('rememberMeToken', rememberMeToken);
    }

    public get rememberMeToken() {
        return localStorage.getItem('rememberMeToken');
    }

    public get user() {
        return localStorage.getItem('token');
    }

    public set user(user: any) {
        localStorage.setItem('token', JSON.stringify(user));
    }

    public get isWaitingForAuthToComplete() {
        const value = localStorage.getItem('isWaitingForAuthToComplete');
        return (value === 'true') ? true : false;
    }

    public set isWaitingForAuthToComplete(value: any) {
        localStorage.setItem('isWaitingForAuthToComplete', value);
    }

}



