
export class CharacTypeComplementType {

    public id: number;
    public label: string;
    public mandatory: string;
    public tenantId: number;
    public id_complement_type: number;
    public type: string;

    constructor(obj?: any) {
        this.id = obj && obj.id || null;
        this.label = obj && obj.label || null;
        this.mandatory = obj && obj.mandatory || null;
        this.tenantId = obj && obj.tenantId || null;
        this.id_complement_type = obj && obj.id_complement_type || null;
        this.type = obj && obj.type || null;
    }
}


