import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { SchedulerEvent } from '@progress/kendo-angular-scheduler';
import * as moment from 'moment';
import { ResourceService } from 'app/services/resource/resource.service';
import { TenantConfigurationService } from 'app/modules/core';
import { InfoLocalStorageService } from 'app/modules/localStorage';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-show-calendar',
  templateUrl: './show-calendar.component.html',
  styleUrls: ['./show-calendar.component.css']
})
export class ShowCalendarComponent implements OnInit {

  @Input() id: number;
  @Input() label: string;
  public selectedDate: Date = new Date();
  //All day
  public startTime = '07:00';
  public endTime = '19:00';
  //Business hours
  public workDayStart = '07:00';
  public workDayEnd = '19:00';
  public loading: boolean = true;
  public events: SchedulerEvent[] = [];
  subscription: any;
  url: SafeResourceUrl;
  constructor(private route: ActivatedRoute,
    private router: Router,
    private sanitizer: DomSanitizer,
    private activeModal: NgbActiveModal,
    private resourceService: ResourceService,
    private translateService: TranslateService,
    private tenantConfigurationService: TenantConfigurationService) {

  }
  ngOnInit() {
    this.selectedDate = new Date();
    this.workDayStart = this.tenantConfigurationService.Reports_StartHour;
    this.workDayEnd = this.tenantConfigurationService.Reports_EndHour;
    this.loadData();

  }
  public decline() {
    this.activeModal.close(false);
  }
  public dismiss() {
    this.activeModal.dismiss();
  }

  public onEventClick({ sender, event }): void {

    moment.locale('fr');
    let title: string = this.translateService.instant('TOOLTIP-NO-TITLE');
    if (event.title) {
      title = event.title;
    }
    let strTmp = title
      + '\n\n' + this.translateService.instant('TOOLTIP-START') + moment(event.start).format('dddd DD/MM/YYYY HH:mm')
      + '\n' + this.translateService.instant('TOOLTIP-END')  + moment(event.end).format('dddd DD/MM/YYYY HH:mm')
      + '\n\n' + this.translateService.instant('TOOLTIP-LOCATION') + event.description;

    alert(strTmp);
  }
  public loadData(): void {
    var idResource: any;
    //let data: any[] = [0];
    var data: any;
    var data2: any;
    data = [0];
    this.events = [];
    this.loading = true;
    //Get Data
    this.getData()
      .then(r => {
        data = r;
        data.forEach(element => {
          element.description = this.label;
        });
        this.events = data;
        this.loading = false;
      }
      )
      .catch(err => {
        // console.log("err => chargement des événements");
        this.loading = false;
      })
  }
  getData() {
    // keep this for Promise
    var that = this;


    return new Promise(function (resolve, reject) {
      that.resourceService.getCalendar(
        that.id).subscribe(
          data => {
            resolve(data);
          },
          error => {
            reject();
          })
    });
  }
  // getColor(date): any{
  //   let today= new Date();
  //   if(date===today){
  //      return {'color': 'red', 'font-size': '24px', 'font-weight': 'bold'}
  //   } else {
  //      return {'color': 'blue', 'font-size': '24px', 'font-weight': 'bold'}
  //   }
  // }
}



