import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { AuthGuard } from 'app/modules/authentication/services/auth.guard';
import { LoginComponent } from 'app/modules/authentication/views/login.component';

const checkupRoutes: Routes = [
    {
        path: 'login',
        component: LoginComponent,
    },
];

@NgModule({
    imports: [
        RouterModule.forChild(checkupRoutes),
    ],
    exports: [
        RouterModule,
    ],
})
export class AuthenticationRoutingModule { }



