import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { AuthGuard } from 'app/modules/authentication/services/auth.guard';
import { AuthService } from 'app/modules/authentication/services/auth.service';
import { LoginComponent } from 'app/modules/authentication/views/login.component';
import { AuthenticationRoutingModule } from 'app/modules/authentication/authentication-routing.modules';
import { CoreModule } from 'app/modules/core';
import { TranslateModule } from '@ngx-translate/core';
import { Office365Module } from 'app/modules/authentication/modules/office365/office365.module';
import { ZenOfficeModule } from 'app/modules/authentication/modules/zenoffice/zenoffice.module';

@NgModule({
    imports: [
        BrowserModule,
        AuthenticationRoutingModule,
        CoreModule,
        TranslateModule.forChild(),
        Office365Module,
        ZenOfficeModule,
    ],
    declarations: [
        LoginComponent,
    ],
    providers: [
        AuthGuard,
        AuthService,
    ],
    exports: [

    ],
})
export class AuthenticationModule { }



