import { Injectable, Inject } from '@angular/core';

import { API_ROUTES_NAME } from './routes/api';

import { environment } from 'environments/environment';
import { AppConfigurationService } from './app-configuration.service';

@Injectable()
export class PathService {

    private apiPathWeb: string = '';

    // inject our ROUTES
    constructor(private appConfigurationService: AppConfigurationService) {
        //constructor() {
        //this.apiPathWeb = environment.api;
        this.apiPathWeb = this.appConfigurationService.urlAPIV1;
    }

    public getApiPath(key): string {
        let ret: string = '';

        ret = this.apiPathWeb;

        if (API_ROUTES_NAME[key]) {
            ret += API_ROUTES_NAME[key];
        }

        return ret;
    }
    
    public logout(): void  {
        localStorage.clear();
        //this.router.navigate(['login']);
        window.location.href = this.appConfigurationService.urlV2 + 'login';

    }
}



